import * as moment from 'moment'

export class Servicos {
  constructor(
      public id?: number,
      public titulo?: string,
      public status?: string,
      public prazo?: number,
      public valor?: any,
      public username_user?: string,
      public tipo?: string,
      public progresso?: number,
      public created_at?: Date,
      public updated_at?: Date,
      public progressActive?: boolean,
      public prazojob ?: string
     ) {

      if(this.valor > 0) this.valor = this.valor.toFixed(2);
      if(this.created_at){
        this.prazojob = moment(this.created_at).subtract(this.prazo, 'days').fromNow()
          console.log(moment(this.created_at))
        }

     }
}
