import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import localePt from '@angular/common/locales/pt';
import { registerLocaleData } from '@angular/common';
registerLocaleData(localePt, 'pt');

// Modulos de Terceiros
import { AppComponent } from './app.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import 'materialize-css';

// Meus Modulos
import { PublicModule } from './public/public.module';
import { AppRoutingModule } from './app-routing.module';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { FooterComponent } from './footer/footer.component';
import { NavbarComponent } from './navbar/navbar.component';
import { RestritoModule } from './arearestrita/restrito.module';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AutenticationComponent } from './autentication/autentication.component';
import { AuthInterceptorService } from './auth/auth-interceptor.service';

// import { Mercadopago } from '../../node_modules/mercadopago/lib/mercadopago.js';


@NgModule({
  declarations: [
    AppComponent,
    PageNotFoundComponent,
    FooterComponent,
    NavbarComponent,
    AutenticationComponent
  ],
  imports: [
    BrowserModule,
    PublicModule,
    RestritoModule,
    FormsModule,
    AppRoutingModule,
    HttpClientModule,
    BrowserAnimationsModule,
    ReactiveFormsModule,
    ReactiveFormsModule
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptorService,
      multi: true,
     }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
