import { Injectable } from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ModificacaoManageService {

  private readonly API =  `${environment.API}`;

  constructor(private auth: AuthService, private http: HttpClient) { }


  //GET - PARAMETROS-------------------------------
  //Variaveis Globais

  variaveisGlobais() {
    if(this.auth.isLoggedIn) {
      return this.http.get(this.API + 'variaveisGlobais');
    }
  }

  //Variaveis Repasse

  variaveisRepasse() {
    if(this.auth.isLoggedIn) {
      return this.http.get(this.API + 'variaveisRepasse');
    }
  }

  //Variaveis comuns

  variaveisComuns(){
    if(this.auth.isLoggedIn) {
      return this.http.get(this.API + 'variaveisComuns');
    }
  }

  //Prices serviços

  mdprice(){
    return this.http.get<any>(this.API + 'mdprice');
  }

  avulsoprice(){
    return this.http.get<any>(this.API + 'avulsoprice');
  }

  boostsoloprice(){
    return this.http.get<any>(this.API + 'boostsoloprice');
  }

  boostduoprice(){
    return this.http.get<any>(this.API + 'boostduoprice');
  }
  //-----------------------------------------------
  //PUT - PARAMETROS-------------------------------

  mdpriceUpdate(fila,data) {
    console.log(data);
    if(fila){
      if(this.auth.isLoggedIn())
      return this.http.put(this.API + 'mdpriceUpdateDuo', {data}).subscribe(console.log);
    }else{
      if(this.auth.isLoggedIn()){
        console.log("MDSOLO");
        return this.http.put(this.API + 'mdpriceUpdateSolo', {data}).subscribe(console.log);
      }
    }
  }

  avulsopriceUpdate(fila,data) {
    console.log(data);
    if(fila){
      if(this.auth.isLoggedIn())
      return this.http.put(this.API + 'avulsopriceUpdateDuo', {data}).subscribe(console.log);
    }else{
      if(this.auth.isLoggedIn()){
        console.log("AvulsoSOLO");
        return this.http.put(this.API + 'avulsopriceUpdateSolo', {data}).subscribe(console.log);
      }
    }
  }

  bootspriceUpdate(fila,data) {
    console.log(data);
    if(fila){
      if(this.auth.isLoggedIn())
      return this.http.put(this.API + 'boostpriceUpdateDuo', {data}).subscribe(console.log);
    }else{
      if(this.auth.isLoggedIn()){
        console.log("BoostSOLO");
        return this.http.put(this.API + 'boostpriceUpdateSolo', {data}).subscribe(console.log);
      }
    }
  }

  variaveisUpdate(tipo,data) {
    console.log(data);
    switch (tipo) {
      case 0:
        if(this.auth.isLoggedIn()){
          console.log("Globais");
          return this.http.put(this.API + 'variaveisGlobaisUpdate', {data}).subscribe(console.log);
        }
        break;

      case 1:
        if(this.auth.isLoggedIn())
          return this.http.put(this.API + 'variaveisComunsUpdate', {data}).subscribe(console.log);

        break;

      case 2:
        if(this.auth.isLoggedIn())
          return this.http.put(this.API + 'variaveisRepasseUpdate', {data}).subscribe(console.log);
        break;

      default:
        break;
    }
  }









}
