import { UserManageService } from './../../../services/user-manage.service';
import { JobberManageService } from './../../../services/jobber-manage.service';

import { switchMap, catchError, tap } from 'rxjs/operators';
import { Observable, empty } from 'rxjs';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Servicos } from 'src/app/arearestrita/classes/servicos';

declare var M:any;

@Component({
  selector: 'app-job-detalhe',
  templateUrl: './job-detalhe.component.html',
  styleUrls: ['./job-detalhe.component.css']
})
export class JobDetalheComponent implements OnInit {

  isRateLimitReached = false;
  profile$: Observable<{Servicos:Servicos}>;
  status: boolean;
  service;


  constructor(
    private route: ActivatedRoute,
    private jobber: JobberManageService,
    private user: UserManageService
    ) { }

  ngOnInit(): void {

    this.profile$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
      { this.service = params.get('id');
        return this.jobber.service(params.get('id'));}
     ),
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado.'})
        this.isRateLimitReached = true;
        return empty();
      }),
      tap(console.log)
    );
  }

  onAceitar(){
    this.jobber.aceitarJob(this.user.getUser(),this.service).pipe(
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado. Operanção não finalizada!'})
        return empty();
      })
    ).subscribe(
      data => this.status = true
    );
  }

  onRejeitar(){
    console.log('Voltar');
  }


}
