import { Perfil } from './../../../classes/Perfil';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { switchMap, catchError } from 'rxjs/operators';
import { Observable, empty } from 'rxjs';
import { UserManageService } from 'src/app/arearestrita/services/user-manage.service';

declare var M:any;

@Component({
  selector: 'app-perfil-detalhe',
  templateUrl: './perfil-detalhe.component.html',
  styleUrls: ['./perfil-detalhe.component.css']
})
export class PerfilDetalheComponent implements OnInit {
// ---------------------------------------------------- DETALHE ------------------------------------------------------------
  // Session 1
  labels1=[
    ['Nome'],
    ['Email'],
    ['Contato'],
    ['Username']
  ]

  campos1=[
    ['firstName','lastName'],
    ['address'],
    ['contact'],
    ['username']
  ]

  defineData1=[
    'simples',
    'simples',
    'simples',
    'simples'
  ]

  acoes: Function[] = [
  ];

  acaoTitulo = [
  ];

  sessions = [
    [this.labels1,this.campos1,this.defineData1]
  ]

  titleSessions = [
    ["Dados Pessoais"]
  ]

  data = 'username';

  rota = 'admProfile';

// ---------------------------------------------------- DETALHE ------------------------------------------------------------
// ---------------------------------------------------- LISTAGEM ------------------------------------------------------------

displayedColumns: string[] =[
  'id',
 'titulo',
 'jobber_username',
 'status',
 'prazo',
 'opcoes'];
columnsDefinies: string[] = [
  'prefix',
 'simples',
 'existe',
 'simples',
  'sufix',
'op'];
columnsDisplay: string[] = [
  'Pedido',
  'Titulo',
  'Jobber',
  'Status',
  'Prazo'];
columnsAttr: any[any] = [
  ['id', 'Pedido#'],
 'titulo',
 ['jobber_username','atribuido','Nenhum Jobber designado'],
 'status',
 ['prazo','dia(s)'],
  [['../servico/','Detalhes','id']]];

refLista = 'username';
rotaLista = 'admServicosCliente';
// ---------------------------------------------------- LISTAGEM ------------------------------------------------------------


  isRateLimitReached = false;
  profile$: Observable<Perfil>;

  constructor(
    private user: UserManageService,
    private route: ActivatedRoute,
    private router: Router
    ) {



   }
  ngOnInit(): void {

    this.profile$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
      {return this.user.profile(params.get('username'));}
     ),
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado.'})
        this.isRateLimitReached = true;
        return empty();
      })
    );
  }


}
