import { Component, OnInit, ViewChild } from '@angular/core';
import { DetalheComponent } from '../../detalhe/detalhe.component';
import { AdmManageService } from 'src/app/arearestrita/services/adm-manage.service';
import { ActivatedRoute } from '@angular/router';
import { catchError } from 'rxjs/operators';
import { empty } from 'rxjs';

declare var M:any;

@Component({
  selector: 'app-servico-detalhe',
  templateUrl: './servico-detalhe.component.html',
  styleUrls: ['./servico-detalhe.component.css']
})
export class ServicoDetalheComponent implements OnInit {

  // ---------------------------------------------------- Orientado a DADOS ------------------------------------------------------------
  // Session 1
  labels1=[
    ['Pedido'],
    ['Cliente'],
    ['Titulo'],
    ['tipo'],
    ['Status'],
    ['Progresso'],
    ['Valor'],
    ['Prazo'],
    ['Contratado']
  ]

  campos1=[
    ['id'],
    ['username_user'],
    ['titulo'],
    ['tipo'],
    ['status'],
    ['progresso'],
    ['valor'],
    ['prazo'],
    ['created_at']
  ]

  defineData1=[
    'simples',
    'simples',
    'simples',
    'simples',
    'simples',
    'progresso',
    'simples',
    'simples',
    'data'
  ]

  // Session 2

  labels2=[
    ['Username Jobber'],
    ['Contratado'],
  ]

  campos2=[
    ['jobber_username'],
    ['created_at']
  ]

  defineData2=[
    'simples',
    'data'
  ]

  acaoTitulo = [
    'Aceitar',
    'Rejeitar',
    'Reservar'
  ];

  sessions = [
    [this.labels1,this.campos1,this.defineData1],
    [this.labels2,this.campos2,this.defineData2]
  ]

  titleSessions = [
    ["Serviço"],
    ["Contratação"]
  ]

  data = 'id';

  rota = 'admServico';

  ref;

  @ViewChild(DetalheComponent, {static: true})
  private detalhe: DetalheComponent;

  // ---------------------------------------------------- Orientado a DADOS ------------------------------------------------------------

  constructor(
    private adm: AdmManageService,
    private route: ActivatedRoute
  ){
    this.ref = this.route.snapshot.paramMap.get(this.data);
  }
  ngOnInit(): void {
  }

  reacao(acao: string){

    switch (acao) {
      case 'Acaitar':
        this.onAceitar();
        break;
        case 'Rejeitar':
        this.onRejeitar();
        break;
        case 'Reservar':
        this.onReservar();
        break;

      default:
        break;
    }

  }

  onAceitar(){
    console.log("Aceitou")
    this.adm.jobberAceitar(this.ref).pipe(
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado. Operanção não finalizada!'})
        return empty();
      })
    ).subscribe(
      data => this.detalhe.status = true
    );
  }

  onReservar(){
    console.log("reservou")
    console.log(this.ref)
    this.adm.jobberReservar(this.ref).pipe(
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado. Operanção não finalizada!'})
        return empty();
      })
    ).subscribe(
      data => this.detalhe.status = true
    );
  }
  onRejeitar(){
    console.log("rejeitou")
    console.log(this.ref)
    this.adm.jobberRejeitar(this.ref).pipe(
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado. Operanção não finalizada!'})
        return empty();
      })
    ).subscribe(
      data => this.detalhe.status = true
    );
  }


}
