import * as moment from "moment";
export class Pagamento {
  constructor(
      public comprovante: string,
      public contract_id: number,
      public jobber_username: string,
      public status: string,
      public statusPago: string,
      public tipo :string,
      public titulo: string,
      public valor: any,
      public valorPago: any,
      public service_id?: any
    ) {
      if(this.valor > 0) this.valor = this.valor.toFixed(2);
      if(this.valorPago > 0) this.valorPago = this.valorPago.toFixed(2);


     }
}
