import { CandidatoJobber } from './../../public/classes/candidato-jobber';
import { Perfil } from './../classes/Perfil';
import { Paginator } from './../classes/paginator';
import { Observable } from 'rxjs';
import { AuthService } from 'src/app/auth/auth.service';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { tap, delay } from 'rxjs/operators';
import { environment } from 'src/environments/environment';


  @Injectable({
    providedIn: 'root',
  })
export class UserManageService {
  listaClientesBusca(active: string, direction: string, paginator: import("@angular/material/paginator").MatPaginator) {
    throw new Error("Method not implemented.");
  }

  private readonly API =  `${environment.API}`;

  // Instancia Salva user
  user;

  constructor(
    private http: HttpClient,
    private auth: AuthService
    ) { }

  instance(data){
    this.user = data;
    console.log(this.user);
  }

  getUser(){
    return localStorage.getItem('username');
  }

  profile(user) {
    if(this.auth.isLoggedIn())
      return this.http.get<Perfil>(this.API + 'profile/'+user).pipe(delay(500));
  }

  profileUpdate(username,data) {
    console.log(data);
    if(this.auth.isLoggedIn())
      return this.http.put(this.API + 'profileUpdate', {username,data}).subscribe(console.log);
  }

  //Funções areas restritas







}
