import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class PricesService {

  private readonly API =  `${environment.API}`;

  constructor(private http: HttpClient) { }

  mdprice(){
    return this.http.get<any>(this.API + 'mdprice');
  }

  avulsoprice(){
    return this.http.get<any>(this.API + 'avulsoprice');
  }

  boostsoloprice(){
    return this.http.get<any>(this.API + 'boostsoloprice');
  }

  boostduoprice(){
    return this.http.get<any>(this.API + 'boostduoprice');
  }

}
