import { Avulso } from './../classes/avulso';
import { NavService } from './../services/nav.service';
import { CompraService } from './../services/compra.service';
import { AuthService } from './../../auth/auth.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { PricesService } from '../services/prices.service';

declare var M: any;


@Component({
  selector: 'app-vitorias-avulsas',
  templateUrl: './vitorias-avulsas.component.html',
  styleUrls: ['./vitorias-avulsas.component.css']
})
export class VitoriasAvulsasComponent implements OnInit {

  // Consulta de preço
  priceSolo = [];
  priceDuo = [];

  // Total Compra
  total = 0;
  prazo;

  // Formulario
  compra;
  disabled;
  pedido;

  // [Auth] Coerencia no fluxo de login
  isLogged;
  /////////////////////////////

  // [Integração] Consultar diretrizes do site,

  divisoes = ['FERRO', 'BRONZE', 'PRATA', 'OURO', 'PLATINA', 'DIAMANTE', 'MESTRE'];
  filas = ['SOLO', 'DUO', 'FLEX' ];
  ////////// client-side-server ///////////////

  constructor(
    private formBuilder: FormBuilder,
    private auth: AuthService,
    private price: PricesService,
    private comprar: CompraService,
    private nav:NavService
     ) {

    this.compra = this.formBuilder.group({
      divisao_form: ['FERRO', Validators.required],
      fila_form: ['SOLO', Validators.required],
      quantidade_form: ['2', Validators.required],
    });
    // check Login
    this.isLogged = this.auth.isLoggedIn();
  }

  ngOnInit() {
    // [materialize] Select buttons
    document.addEventListener('DOMContentLoaded', function() {
      const elems = document.querySelectorAll('select');
      const instances = M.FormSelect.init(elems);
    });


    // [Servidor] Comunição servidor price
    this.price.avulsoprice().subscribe(dados => {
      // Conferir a ordem dos preços
      this.replacePrices(dados);
      console.log(dados);
    });

    // Instancia de observable change form
    this.onChanges();

    this.disabled = false;
  }

  replacePrices(dados){

    this.priceSolo = dados.priceSolo;
    this.priceDuo = dados.priceDuo;

    // Inicializar valor
    this.valorLive(this.compra.value);
  }

  onChanges(): void {
    this.compra.valueChanges.subscribe(form => {
      this.valorLive(form);
    });
  }

  valorLive(form) {
    console.log(form);
    console.log(this.disabled);
    console.log(this.priceSolo);
    this.total = 0;

    if(form.quantidade_form >= 2 && form.quantidade_form <= 10) {
      if (form.fila_form === 'SOLO') {
        console.log(form.divisao_form);
        this.total = this.priceSolo[form.quantidade_form  - 2][form.divisao_form];
      } else if (form.fila_form === 'DUO') {
        this.total = this.priceDuo[form.quantidade_form  - 2][form.divisao_form];
      } else if (form.fila_form === 'FLEX') {
        this.total = this.priceDuo[form.quantidade_form  - 2][form.divisao_form];
      }
      this.prazo = this.priceSolo[form.quantidade_form  - 2]['prazo'];
    }else {
      this.total = 0;
      this.disabled = true;
      this.prazo = 0;
    }


    if(this.total === 0 ) this.disabled = true;
    else this.disabled = false;

  }

  onSubmit(data) {
    if (this.isLogged){
      console.log(data);
      this.pedido = new Avulso(data["fila_form"],data["divisao_form"],data["quantidade_form"],this.total, this.prazo);
      this.comprar.compraAvulso(this.pedido);
    } else {
      this.nav.onOpen();
    }
  }


}
