import * as moment from "moment";
export class Jobbers {
  constructor(
      public username: string,
      public firstName: string,
      public lastName: string,
      public progresso: number,
      public created_at:string,
      public status: string,
      public ingress?: string,
      public progressActive?: boolean
    ) {
      moment.locale('pt-br');
      console.log(moment(this.created_at).fromNow());
      this.created_at = moment(this.created_at).fromNow();
      if(typeof this.progresso == 'number') this.progressActive = true;
      else this.progressActive = false;
     }
}
