import { Pagamento } from './../classes/pagamento';
import { Contratacao } from './../classes/contratacao';
import { Injectable } from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { HttpClient } from '@angular/common/http';
import { CandidatoJobber } from 'src/app/public/classes/candidato-jobber';
import { Perfil } from '../classes/Perfil';
import { Servicos } from '../classes/servicos';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DetalheService {
  private readonly API =  `${environment.API}`;

  rota:string;

  status;

  constructor(private auth: AuthService, private http: HttpClient) { }

  detalhar(ref ,rota){
    console.log(rota);
    this.rota = rota;
    switch (rota) {
      case 'admCandidato':
        return this.candidato(ref);
      case 'admProfile':
        return this.profile(ref);
      case 'admServico':
        return this.service(ref);
      case 'admPagamento':
        return this.pagamento(ref);
      case 'admContratacao':
        return this.contratacao(ref);

      // case '':
      //   return this.listaCandidatos(o,d,p);

      default:
        break;
    }
  }

  candidato(user) {
    console.log("candidato")
    if(this.auth.isLoggedIn())
      return this.http.get<CandidatoJobber>(this.API + 'candidato/'+user);
  }

  profile(user) {
    if(this.auth.isLoggedIn())
      return this.http.get<Perfil>(this.API + 'profile/'+user);
  }

  service(id) {
    if(this.auth.isLoggedIn())
      return this.http.get<{Servicos:Servicos,Contratacao:Contratacao}>(this.API + 'adm/service/'+id);
  }

  pagamento(id) {
    if(this.auth.isLoggedIn())
      return this.http.get<{Pagamento:Pagamento}>(this.API + 'adm/pagamento/'+id);
  }

  contratacao(id) {
    if(this.auth.isLoggedIn())
      return this.http.get<{Contratacao:Contratacao}>(this.API + 'adm/contratacao/'+id);
  }

  

}
