
import { Component, OnInit, ViewChild } from '@angular/core';
import { Observable, empty } from 'rxjs';
import { ActivatedRoute, Router, ParamMap } from '@angular/router';
import { switchMap, catchError, tap } from 'rxjs/operators';
import { DetalheComponent } from '../../detalhe/detalhe.component';
import { AdmManageService } from 'src/app/arearestrita/services/adm-manage.service';

declare var M:any;

@Component({
  selector: 'app-candidato-perfil',
  templateUrl: './candidato-perfil.component.html',
  styleUrls: ['./candidato-perfil.component.css']
})
export class CandidatoPerfilComponent implements OnInit {

  // ---------------------------------------------------- Orientado a DADOS ------------------------------------------------------------
  // Session 1
  labels1=[
    ['Nome'],
    ['Email'],
    ['Contato'],
    ['ingresso']
  ]

  campos1=[
    ['firstName','lastName'],
    ['address'],
    ['contact'],
    ['ingress']
  ]

  defineData1=[
    'simples',
    'simples',
    'simples',
    'simples'
  ]

  // Session 2

  labels2=[
    ['Elo'],
    ['Nivel'],
    ['Divisão Máxima Solo'],
    ['Divisão Máxima Duo'],
  ]

  campos2=[
    ['elo'],
    ['nivel'],
    ['divisaoMaxSolo'],
    ['divisaoMaxDuo']
  ]

  defineData2=[
    'simples',
    'simples',
    'simples',
    'simples'
  ]

  // Session 3

  labels3=[
    ['Rotas Preferidas',['Rota Inferior', 'Rota Superior', 'Rota Central', 'Rota Selva']],
    ['Horários',['Segunda', 'Terça', 'Quarta','Quinta', 'Sexta', 'Sábado', 'Domingo']],
    ['Experiência'],
    ['Motivação'],
    ['Complemento'],
  ]

  campos3=[
    ['rota_inf','rota_sup', 'rota_mid', 'rota_jun'],
    ['segunda', 'terca', 'quarta', 'quinta', 'sexta', 'sabado', 'domingo'],
    ['experiencia'],
    ['motivacao'],
    ['complemento']
  ]

  defineData3=[
    'multipla',
    'porcentagem',
    'texto',
    'texto',
    'texto'
  ]


  acaoTitulo = [
    'Aceitar',
    'Rejeitar',
    'Reservar'
  ];

  descricaoAcao = "Entre as ações, você pode Aceitar o colaborar para fazer parte do sistema, ou Rejeitar o que excluira o candidato da base de dados, ou reservar, isso atribui o status de reserva para futura convocação."

  sessions = [
    [this.labels1,this.campos1,this.defineData1],
    [this.labels2,this.campos2,this.defineData2],
    [this.labels3,this.campos3,this.defineData3]
  ]

  titleSessions = [
    ["Dados Pessoais"],
    ["Parametros do League of Legends"],
    ["Candidatura"]
  ]

  data = 'username';

  rota = 'admCandidato';

  ref;

  @ViewChild(DetalheComponent, {static: true})
  private detalhe: DetalheComponent;

  // ---------------------------------------------------- Orientado a DADOS ------------------------------------------------------------

  constructor(
    private adm: AdmManageService,
    private route: ActivatedRoute
  ){
    this.ref = this.route.snapshot.paramMap.get(this.data);
  }
  ngOnInit(): void {
  }

  reacao(acao: string){
    console.log(acao)
    switch (acao) {
      case 'Aceitar':
        this.onAceitar();
        break;
        case 'Rejeitar':
        this.onRejeitar();
        break;
        case 'Reservar':
        this.onReservar();
        break;

      default:
        break;
    }

  }

  onAceitar(){
    console.log("Aceitou")
    this.adm.jobberAceitar(this.ref).pipe(
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado. Operanção não finalizada!'})
        return empty();
      })
    ).subscribe(
      data => this.detalhe.status = true
    );
  }

  onReservar(){
    console.log("reservou")
    console.log(this.ref)
    this.adm.jobberReservar(this.ref).pipe(
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado. Operanção não finalizada!'})
        return empty();
      })
    ).subscribe(
      data => this.detalhe.status = true
    );
  }
  onRejeitar(){
    console.log("rejeitou")
    console.log(this.ref)
    this.adm.jobberRejeitar(this.ref).pipe(
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado. Operanção não finalizada!'})
        return empty();
      })
    ).subscribe(
      data => this.detalhe.status = true
    );
  }


}
