import { UserManageService } from './../../arearestrita/services/user-manage.service';
import { Injectable } from '@angular/core';

declare var M:any;

@Injectable({
  providedIn: 'root'
})
export class NavService {



  constructor(

  ) { }

  hierarquiaAcesso(type){
    if(type === '0')  return 'adm';
    if(type === '1')  return 'jobber';
    if(type === '2')  return 'cliente';
    return 'erro';
  }

  onOpen(){
    var elem = document.querySelector('.modal');
    var instance = M.Modal.getInstance(elem);
    instance.open();
  }
}
