import { NavService } from './../public/services/nav.service';
import { UserManageService } from './../arearestrita/services/user-manage.service';
import { AuthService } from './../auth/auth.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, Validators } from '@angular/forms';
import { empty } from 'rxjs';
import { catchError, map, startWith } from 'rxjs/operators';
declare var $: any;
declare var M: any;

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css'],
  providers: [UserManageService]
})
export class NavbarComponent implements OnInit {

  // Login
  isLogged;

  //Usename
  username;

  //Area restrita
  home;



  routes = [
    {route: 'trabalhe-conosco', nome: 'Trabalhe Conosco', id:'conosco'},
    {route: 'servicos', nome: 'Serviços', id:'arearestrita'},
    {route: 'conta-a-venda', nome: 'Contas à Venda', id:'compra'},
    {route: 'md10', nome: 'MD10', id:'arearestrita'},
    {route: 'vitorias-avulsas', nome: 'Vitórias Avulsas', id:'avulsas'},
    {route: 'boost-solo', nome: 'Eloboost', id:'boostsolo'},
    {route: 'boost-duo', nome: 'Duoboost', id:'boostduo'},
    {route: '', nome: 'Início', id:'inicio'}
  ];


  singin;
  singup;

  error = false;
  loadingsignin = false;
  loadingsignup = false;
  authfail = false;

  //Check username
  isLoadingcheck: boolean = false;
  isCheckuser: boolean = true;
  valueCheckuser: string ='';
  isChecktrain:boolean = false;
  isLenghtUser: boolean = false;
  minimumLenghtUser = 6;

  //Confirm password
  isComfirmPassword: boolean = false;
  isComfirmPasswordtrain: boolean = false;
  passwordOrigin: string;
  passwordConfirm: string;

  constructor(
    private formBuilder: FormBuilder,
    private auth: AuthService,
    private nav: NavService,
    private route: Router
    ) {
    this.singup = this.formBuilder.group({
      firstName: ["", Validators.required],
      lastName: ["", Validators.required],
      contact: [, Validators.required],
      address: ["", Validators.required],
      username: ["", Validators.required],
      password: ["", Validators.required],
    });

    this.singin = this.formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
    });

  }
  ngOnInit() {

    var elems = document.querySelectorAll('#arearestrita.modal');
    var instances = M.Modal.init(elems);
    this.isLogged=this.auth.isLoggedIn();
    if(this.isLogged){
      this.home = this.nav.hierarquiaAcesso(localStorage.getItem('type'));
      this.username = localStorage.getItem('username');
      M.AutoInit();
      this.route.navigate(['/'+this.home])
    }

    // tslint:disable-next-line: only-arrow-functions
    $(document).ready(function() {
      $('.sidenav').sidenav();
      $('.collapsible').collapsible();

      $('.dropdown-trigger').dropdown();
    });
  }

  onSubmitSignIn(data) {
    // login
    console.log("login:");
    this.loadingsignin =true;
    this.auth.signin(data).pipe(
      catchError(error => {
        if(error.status !== 401){
          this.error = true;

          M.toast({html: 'Erro inesperado ocorreu, tente novamente'});
        }
        else{
          M.toast({html: 'Usuario ou/e senha incorreto.'});
          this.loadingsignin = false;
        }
        return empty();
      })
    ).subscribe(
      resp => {
        console.log("tem resposta")
        if(resp['mensagem']){
          console.log(resp)
          M.toast({html: resp['mensagem']});
        }else{
          console.log(resp)
          this.auth.setSession(resp);
          this.ngOnInit()
        }
        this.loadingsignin = false;

      },error =>{
        console.log("error")
        console.log(error);
        this.loadingsignin = false;
      }
      ,() => {
        console.log('Complete');
      }
      );

  }



  onSubmitSignUp(data) {
    // Cadastro
    console.log("cadastro:");
    this.loadingsignup = true;
    console.log(data);
    this.auth.signupClient(data).pipe(
      catchError(error => {
        if(error.status === 401){
          this.loadingsignup = false;
          M.toast({html: 'Preencha todos os campos!'});
        }
        else this.error = true;
        return empty();
      })
    ).subscribe(
      resp => {
        this.loadingsignup = false;
      },error =>{
        console.log(error);
      }
      ,() => {
        console.log('Complete')
      }
      );
  }

  onSignInFace(){
    console.log("entrou pelo face");
    this.auth.singinFace();
  }


  checkuser(data){
    // verificar se ja tentou
    if(!this.isChecktrain){
      this.isChecktrain = true;
      this.isCheckuser =false;
    }
    // verificar se tem minimo
    if(data.length >= this.minimumLenghtUser)
      this.isLenghtUser = true;
    else this.isLenghtUser = false;



    console.log(data);
    if(data.length){
      this.valueCheckuser = data; //Guarda valor para apresentação
      this.isLoadingcheck = true; // iniciar load
      this.auth.checkuser(data).subscribe(
        (resp:boolean) => {
          this.isCheckuser = resp;
        },
        error =>{
          M.toast({html: 'Serviço indisponivel, tente mais tarde.'});
          console.log(error);
        }
        ,() => {
          this.isLoadingcheck = false;
          console.log('Complete');
        }
      );
    }
  }

  oringinpassword(data){
    this.passwordOrigin = data;
    if(this.passwordConfirm === this.passwordOrigin)
      this.isComfirmPassword = true;
    else this.isComfirmPassword = false;
  }
  confirmpassword(data){
    // verificar se ja tentou
    if(!this.isComfirmPasswordtrain)
      this.isComfirmPasswordtrain = true;
    this.passwordConfirm = data;
    if(this.passwordConfirm === this.passwordOrigin)
      this.isComfirmPassword = true;
    else this.isComfirmPassword = false;
  }

  logoff(){
    this.auth.logout();
  }

}



