import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { Servicos } from '../classes/servicos';
import { environment } from 'src/environments/environment';


@Injectable({
  providedIn: 'root'
})
export class JobberManageService {

  private readonly API =  `${environment.API}`;

  constructor(private auth: AuthService, private http: HttpClient) { }



  listaServicos(ordem,direcao,paginator,username){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'jobber/listaServicos/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao+'/'+username);
    }
  }

  bancoJobs(ordem,direcao,paginator){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'jobber/bancojob/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao);
    }
  }

  service(id) {
    if(this.auth.isLoggedIn())
      return this.http.get<{Servicos:Servicos}>(this.API + 'jobber/service/'+id);
  }

  aceitarJob(user_username,id){
      console.log(user_username);
      if(this.auth.isLoggedIn())
        return this.http.put(this.API + 'aceitarJob', {user_username, id});
  }

  // AREA CRITICA
  cancelarServico(motivo,service_id,jobber_username){
    if(this.auth.isLoggedIn) {
      return this.http.put(this.API + 'jobber/cancelarServico', {motivo, service_id,jobber_username});
    }
  }

  deixarServico(motivo,service_id,jobber_username){
    if(this.auth.isLoggedIn) {
      return this.http.put(this.API + 'jobber/deixarServico', {motivo, service_id,jobber_username});
    }
  }

}


