import { NavService } from './../services/nav.service';
import { Md10 } from './../classes/md10';
import { AuthService } from './../../auth/auth.service';
import { Component, OnInit, AfterViewChecked } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { PricesService } from '../services/prices.service';
import { CompraService } from '../services/compra.service';

declare var M: any;

@Component({
  selector: 'app-m10',
  templateUrl: './m10.component.html',
  styleUrls: ['./m10.component.css']
})
export class MdComponent implements OnInit, AfterViewChecked {

  //Consulta de preço
  priceSolo = [];
  priceDuo = [];
  priceFlex = [];
  //Total Compra
  total;
  disabled;

  //Prazo
  prazo;

  //Formulario
  compra;
  pedido: Md10;

  //[Auth] Coerencia no fluxo de login
  // isLogged;
  /////////////////////////////

  //[Integração] Consultar diretrizes do site
  divisoes = [] ;
  filas = ['SOLO', 'DUO', 'FLEX' ];
  //////////client-side-server///////////////


  constructor(
    private formBuilder: FormBuilder,
    private auth: AuthService,
    private price: PricesService,
    private comprar: CompraService,
    private nav: NavService
  ) {

    this.compra = this.formBuilder.group({
      divisao_form: [0, Validators.required],
      fila_form: ['SOLO', Validators.required],
    });

  }

  ngAfterViewChecked(){
    M.AutoInit();
      var elems = document.querySelectorAll('select');
      var instances = M.FormSelect.init(elems);
  }

  ngOnInit() {

    //[materialize] Select buttons
    document.addEventListener('DOMContentLoaded', function () {
      var elems = document.querySelectorAll('select');
      var instances = M.FormSelect.init(elems);
    });


    //[Servidor] Comunição servidor price
    this.price.mdprice().subscribe(dados => {
      console.log(dados);
      //Conferir a ordem dos preços
      this.config(dados);
      //Inicializar valor
      this.valorLive(this.compra.value);
    });

    //Instancia de observable change form
    this.onChanges();


  }

  config(dados) {
    for (let index = 0; index < dados['priceSolo'].length; index++) {
      //Select Disponivel
      this.divisoes.push(dados['priceSolo'][index]['elo']);
      if(dados['priceSolo'][index]['disponibilidade'])
        // Prices
        this.priceSolo.push([dados['priceSolo'][index]['elo'],dados['priceSolo'][index]['valor'],dados['priceSolo'][index]['prazo']]);
      else
        this.priceSolo.push([dados['priceSolo'][index]['elo'],0,0])
      if(dados['priceDuo'][index]['disponibilidade']){

      // Prices
        this.priceDuo.push([dados['priceDuo'][index]['elo'],dados['priceDuo'][index]['valor'],dados['priceDuo'][index]['prazo']]);
        this.priceFlex.push([dados['priceDuo'][index]['elo'],dados['priceDuo'][index]['valor'],dados['priceDuo'][index]['prazo']]);
      }
      else{
        this.priceDuo.push([dados['priceDuo'][index]['elo'],0,0])
        this.priceFlex.push([dados['priceDuo'][index]['elo'],0,0])
      }
    }
  }

  onChanges(): void {
    this.compra.valueChanges.subscribe(form => {
      console.log(form);
      this.valorLive(form);
    });
  }

  valorLive(form) {
    console.log(this.priceSolo)
    console.log(this.priceDuo)
    console.log(form.divisao_form);
    if (form.fila_form === 'SOLO') {
      this.total = this.priceSolo[form.divisao_form][1];
      this.prazo = this.priceSolo[form.divisao_form][2];
    } else if (form.fila_form === 'DUO') {
      this.total = this.priceDuo[form.divisao_form][1];
      this.prazo = this.priceDuo[form.divisao_form][2];
    } else if (form.fila_form === 'FLEX') {
      this.total = this.priceFlex[form.divisao_form][1];
      this.prazo = this.priceFlex[form.divisao_form][2];
    }

    if(this.total === 0 ) this.disabled = true;
    else this.disabled = false;
  }

  onSubmit(data) {
    if (this.auth.isLoggedIn()) {
      this.pedido = new Md10(this.divisoes[data["divisao_form"]],data["fila_form"],this.total, this.prazo);
      this.comprar.compram10(this.pedido);
    } else {
      this.nav.onOpen();
    }
  }

}
