export class Boostprice {
  public tabela: {
    id: number,
    elo: string,
    divisao: number,
    valor: number,
    prazo: number,
    disponibilidade: number
  }[] = []
  constructor() { }
  pushItem(
    id: number,
    elo: string,
    divisao: number,
    valor: number,
    prazo: number,
    disponibilidade: number
    ) {
    this.tabela.push({
      id,
      elo,
      divisao,
      valor,
      prazo,
      disponibilidade
    })
  }
}
