import { BoostduoComponent } from './boostduo/boostduo.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Meus Componentes
import { HomeComponent } from './home/home.component';
import { ServicosComponent } from './servicos/servicos.component';
import { TrabalheConoscoComponent } from './trabalhe-conosco/trabalhe-conosco.component';
import { MdComponent } from './m10/m10.component';
import { VitoriasAvulsasComponent } from './vitorias-avulsas/vitorias-avulsas.component';
import { CheckoutComponent } from './checkout/checkout.component';
import { BoostSoloComponent } from './boostsolo/boostsolo.component';
import { AdministracaoComponent } from './administracao/administracao.component';


const routes: Routes = [
  {path: 'servicos', component: ServicosComponent},
  {path: 'trabalhe-conosco', component: TrabalheConoscoComponent},
  {path: 'boost-solo', component: BoostSoloComponent},
  {path: 'boost-duo', component: BoostduoComponent},
  {path: 'md10', component: MdComponent},
  {path: 'vitorias-avulsas', component: VitoriasAvulsasComponent},
  {path: 'checkout', component: CheckoutComponent},
  {path: 'administracao', component: AdministracaoComponent},
  {path: '', component: HomeComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PublicRoutingModule { }
