export class Configjobchampions {
  public champions: any = {}
  //Configuração de chamadas do BD
  public service_id : number;
  public colchampions = [];
  public listaChampions = []
  constructor(data) {
    this.colchampions=[];
    for(let champion in data){
      if(champion === 'service_id'){

        this.service_id = data[champion];

      }else{
        if(data[champion]) this.listaChampions.push(data[champion])
      }
      this.colchampions.push(champion);

    }

  }

  enviar(data){
      this.listaChampions = data;
      var camp= 0;
      for(let item in this.colchampions){
        if(this.colchampions[item] === 'service_id') this.champions[this.colchampions[item]] = this.service_id;
        else{
          if(camp < this.listaChampions.length ) this.champions[this.colchampions[item]] = this.listaChampions[camp];
          else this.champions[this.colchampions[item]] = null;
          camp++;
        }
      }
    return this.champions;
  }


}
