import { Candidato } from './../../arearestrita/classes/candidato';
import { AuthService } from 'src/app/auth/auth.service';
import { CandidatoJobber } from './../classes/candidato-jobber';
import { Component, OnInit } from "@angular/core";
import { FormBuilder, Validators, FormControl, FormGroup, ValidatorFn, ValidationErrors } from '@angular/forms';
import { catchError } from 'rxjs/operators';
import { empty, pipe } from 'rxjs';
import { setgid } from 'process';

declare var $: any;
declare var M: any;
@Component({
  selector: "app-trabalhe-conosco",
  templateUrl: "./trabalhe-conosco.component.html",
  styleUrls: ["./trabalhe-conosco.component.css"]
})
export class TrabalheConoscoComponent implements OnInit {

  //Fomulário
  candidato: FormGroup;
  postar: CandidatoJobber;
  rotas_select;

  //Loading
  loading = false;
  error = false;

  categorias = [
    "Ferro",
    "Bronze",
    "Prata",
    "Ouro",
    "Platina",
    "Diamante",
    "Mestre"
  ];

  divisoes = [1, 2, 3, 4];

  elos = []

  teste = true
  dias = [false,false,false,false,false,false,false];

  //Check username
  isLoadingcheck: boolean = false;
  isCheckuser: boolean = true;
  valueCheckuser: string ='';
  isChecktrain:boolean = false;
  isLenghtUser: boolean = false;
  minimumLenghtUser = 6;

  //Confirm password
  isComfirmPassword: boolean = false;
  isComfirmPasswordtrain: boolean = false;
  passwordOrigin: string;
  passwordConfirm: string;

  constructor(
    private formBuilder: FormBuilder,
    private auth: AuthService
  ) {
    for (let index = 0; index < this.categorias.length; index++) {
      for (let div = 0; div < this.divisoes.length; div++) {
        this.elos.push(this.categorias[index]+ " " + this.divisoes[div]);
      }
    }

    this.candidato = this.formBuilder.group({
      firstName: new FormControl(
        '',
        [Validators.required,
        Validators.minLength(3)]
        ),
      lastName: new FormControl(
        '',
        [Validators.required,
        Validators.minLength(3)]
        ),
      contact: new FormControl(
        '',
        [Validators.required,
        Validators.minLength(12)]
        ),
      address: new FormControl(
        '',
        [Validators.required,
        Validators.email]
        ),
      username: new FormControl(
        '',
        [Validators.required,
        Validators.minLength(6)
      ]
        ),
      password: new FormControl(
        '',
        [Validators.required,
        Validators.minLength(8),
      ]
        ),


      nivel: new FormControl(
        '',
        [Validators.required,
        Validators.min(10),
        Validators.max(1000)]
        ),

      elo: new FormControl(
        'Ferro 1',
        [Validators.required]
        ),

      rotas: new FormGroup({
        rota_inf: new FormControl(false),
        rota_sup: new FormControl(false),
        rota_mid: new FormControl(false),
        rota_jun: new FormControl(false),
      }, {validators: pelomenosum}),

      // campeoes: ["", Validators.required],

      divisaoMaxSolo: ["Ferro", Validators.required],
      divisaoMaxDuo: ["Ferro", Validators.required],

      experiencia: ["", Validators.required],
      motivacao: [""],
      complemento: [""],

      horarios: new FormGroup({
        segunda: new FormControl(0),
        terca: new FormControl(0),
        quarta: new FormControl(0),
        quinta: new FormControl(0),
        sexta: new FormControl(0),
        sabado: new FormControl(0),
        domingo: new FormControl(0)
      }, {validators: minimohoras})
    });

  }

  ngOnInit() {

    this.candidato.valueChanges.subscribe(form => {
console.log(form);
console.log(this.candidato);
console.log(this.isCheckuser,this.isChecktrain)
console.log(this.candidato.invalid,!(this.isCheckuser&&this.isChecktrain),!this.isComfirmPassword)
    });

    $("#username").focus(function(){
      console.log("focus");
    });
    $(document).ready(function() {
      $("select").formSelect();
      $("input#input_text, textarea").characterCounter();
      $('.collapsible').collapsible();
      $('.tooltipped').tooltip();
    });
  }

  onSubmit(data) {
    console.log(data)

    console.log("tentou enviar")
    // this.loading = true;
    const jobber = new CandidatoJobber(
      data.firstName,
      data.lastName ,
      data.contact ,
      data.address ,
      data.username ,
      data.password ,
      data.nivel ,
      data.elo ,
      data.rotas.rota_inf ,
      data.rotas.rota_sup ,
      data.rotas.rota_mid ,
      data.rotas.rota_jun ,
      data.divisaoMaxSolo ,
      data.divisaoMaxDuo ,
      null ,
      data.experiencia ,
      data.motivacao ,
      data.complemento ,
      data.horarios.segunda ,
      data.horarios.terca ,
      data.horarios.quarta ,
      data.horarios.quinta ,
      data.horarios.sexta ,
      data.horarios.sabado ,
      data.horarios.domingo

    );
    console.log(jobber);
    this.auth.signupJobber(jobber)
    .pipe(
      catchError(error => {
        if(error.status === 401){
          this.loading = false;
          M.toast({html: 'Preencha todos os campos!'});
        }
        else this.error = true;
        return empty();
      })
    ).subscribe(
      resp => {
        this.loading = false;
      },error =>{
        console.log(error);
      }
      ,() => {
        console.log('Complete')
      }
      );
  }

  checkuser(data){
    // verificar se ja tentou
    if(!this.isChecktrain){
      this.isChecktrain = true;
      this.isCheckuser =false;
    }
    // verificar se tem minimo
    if(data.length >= this.minimumLenghtUser)
      this.isLenghtUser = true;
    else this.isLenghtUser = false;



    console.log(data);
    if(data.length){
      this.valueCheckuser = data; //Guarda valor para apresentação
      this.isLoadingcheck = true; // iniciar load
      this.auth.checkuser(data).subscribe(
        (resp:boolean) => {
          this.isCheckuser = resp;
        },
        error =>{
          M.toast({html: 'Serviço indisponivel, tente mais tarde.'});
          console.log(error);
        }
        ,() => {
          this.isLoadingcheck = false;
          console.log('Complete');
        }
      );
    }
  }

  oringinpassword(data){
    this.passwordOrigin = data;
    if(this.passwordConfirm === this.passwordOrigin)
      this.isComfirmPassword = true;
    else this.isComfirmPassword = false;
  }
  confirmpassword(data){
    // verificar se ja tentou
    if(!this.isComfirmPasswordtrain)
      this.isComfirmPasswordtrain = true;
    this.passwordConfirm = data;
    if(this.passwordConfirm === this.passwordOrigin)
      this.isComfirmPassword = true;
    else this.isComfirmPassword = false;
  }

}

// Validação personalizada

export const minimohoras: ValidatorFn = (control: FormGroup): ValidationErrors | null => {
  const segunda = control.get('segunda');
  const terca = control.get('terca');
  const quarta = control.get('quarta');
  const quinta = control.get('quinta');
  const sexta = control.get('quinta');
  const sabado = control.get('quinta');
  const domingo = control.get('quinta');

  const total = segunda.value  + terca.value  + quarta.value  + quinta.value  + sexta.value  + sabado.value  + domingo.value

  return total >= 10 ? null : {'minimo horas': true} ;
};

export const pelomenosum: ValidatorFn = (control: FormGroup): ValidationErrors | null => {
  const rota_inf = control.get('rota_inf');
  const rota_sup = control.get('rota_sup');
  const rota_mid = control.get('rota_mid');
  const rota_jun = control.get('rota_jun');
if(rota_sup.value || rota_inf.value || rota_mid.value  || rota_jun.value)
  console.log("passou")
else console.log("barrou")
  return (rota_sup.value || rota_inf.value || rota_mid.value  || rota_jun.value) ? null : {'pelo menos um': true} ;
};
