import { FormManageService } from './../../services/form-manage.service';
import { ModificacaoManageService } from './../../services/modificacao-manage.service';
import { Observable, empty } from 'rxjs';
import { Component, OnInit, AfterViewInit, AfterContentInit, AfterViewChecked } from '@angular/core';
import { catchError } from 'rxjs/operators';
import { Mdprice } from '../../classes/prices/mdprice';
import { Avulsoprice } from '../../classes/prices/avulsoprice';
import { Boostprice } from '../../classes/prices/boostprice';
import { Variaveis } from '../../classes/prices/variaveis';

declare var M:any;
declare var $:any;

@Component({
  selector: 'app-modificarvariaveis',
  templateUrl: './modificarvariaveis.component.html',
  styleUrls: ['./modificarvariaveis.component.css']
})
export class ModificarvariaveisComponent implements OnInit, AfterViewChecked{

  variaveis;
  isRateLimitReached = false;
  error= false;

  // variaveis de acesso
  colsAvulsoSolo = []
  colsAvulsoDuo = []
  filaprice = ['priceSolo', 'priceDuo']
  elosSoloBoost = []
  elosDuoBoost = []

  // Formulários
  formMdpriceSolo;
  formMdpriceDuo;
  formAvulsopriceSolo;
  formAvulsopriceDuo;
  formBoostSoloprice;
  formBoostDuoprice;
  formVariaveisGlobais;
  formVariaveisComuns;
  formVariaveisRepasse;


  // tratamento dados
  dataMdprice:Mdprice[] = [];
  dataAvulsoprice:Avulsoprice[] = [];
  dataBoostSoloprice:Boostprice;
  dataBoostDuoprice:Boostprice;
  dataVariaveisComuns:Variaveis;
  dataVariaveisGlobais:Variaveis;
  dataVariaveisRepasse:Variaveis;

  // Observables
  tabelaMdprice$: Observable<any>;
  tabelaAvulsoprice$: Observable<any>;
  tabelaBoostsoloprice$: Observable<any>;
  tabelaBoostduoprice$: Observable<any>;

  variaveisGlobais$: Observable<any>;
  variaveisRepasse$: Observable<any>;

  variaveisComuns$: Observable<any>;
  formBuilder: any;

  // porcentagens$: Observable<Perfil>;

  constructor(
    private mod: ModificacaoManageService,
    private forms:FormManageService,
  ) { }



  ngAfterViewChecked(){
    M.AutoInit();
  }

  ngOnInit() {

    this.variaveisGlobais$ = this.mod.variaveisGlobais().pipe(
      catchError(error => {
      console.error(error);
      M.toast({html: 'Ops, ocorreu um erro inesperado em carregar Variaveis Globais.'})
      this.isRateLimitReached = true;
      return empty();
      }));

    this.variaveisGlobais$.subscribe(
    (data) => {
      console.log("Aqui esta as globais");
      this.dataVariaveisGlobais = new Variaveis();

      for(let item in data){
        this.dataVariaveisGlobais.pushItem(
          data[item]['variaveis'],
          data[item]['valor'],
         );
      }

      console.log("DATA VG:",this.dataVariaveisGlobais.tabela)
      this.formVariaveisGlobais = this.forms.formConstruct('variaveis',this.dataVariaveisGlobais)
      console.log('FOrm VG',this.formVariaveisGlobais);
    }
    );

    this.variaveisRepasse$ = this.mod.variaveisRepasse().pipe(
      catchError(error => {
      console.error(error);
      M.toast({html: 'Ops, ocorreu um erro inesperado em carregar Variaveis Globais.'})
      this.isRateLimitReached = true;
      return empty();
      }));

    this.variaveisRepasse$.subscribe(
    (data) => {
      console.log("Aqui esta as globais");
      this.dataVariaveisRepasse = new Variaveis();

      for(let item in data){
        this.dataVariaveisRepasse.pushItem(
          data[item]['variaveis'],
          data[item]['valor'],
         );
      }

      console.log("DATA VR:",this.dataVariaveisRepasse.tabela)
      this.formVariaveisRepasse = this.forms.formConstruct('variaveis',this.dataVariaveisRepasse)
      console.log('FOrm VR',this.formVariaveisRepasse);
    }
    );

    this.variaveisComuns$ = this.mod.variaveisComuns().pipe(
      catchError(error => {
      console.error(error);
      M.toast({html: 'Ops, ocorreu um erro inesperado em carregar Variaveis Comuns.'})
      this.isRateLimitReached = true;
      return empty();
      }));

    this.variaveisComuns$.subscribe(
      (data) => {
        this.dataVariaveisComuns = new Variaveis();

        for(let item in data){
          console.log(data[item]['variaveis'])
          this.dataVariaveisComuns.pushItem(
            data[item]['variaveis'],
            data[item]['valor'],
           );
        }

        console.log("DATA VC:",this.dataVariaveisComuns.tabela)
        this.formVariaveisComuns = this.forms.formConstruct('variaveis',this.dataVariaveisComuns)
        console.log(this.formVariaveisComuns);
      }
    );

    //tabelas de preços--------------------------------------------
      this.tabelaMdprice$ = this.mod.mdprice().pipe(
      catchError(error => {
      console.error(error);
      M.toast({html: 'Ops, ocorreu um erro inesperado em carregar Preço MD10.'})
      this.isRateLimitReached = true;
      return empty();
      }));

      this.tabelaMdprice$.subscribe( (data) =>{
      //console.log("Md price Solo",data)
      this.dataMdprice.push(new Mdprice());
      for(let item in data[this.filaprice[0]]){
        //console.log(item)
        this.dataMdprice[0].pushItem(
          data[this.filaprice[0]][item]['id'],
          data[this.filaprice[0]][item]['elo'],
          data[this.filaprice[0]][item]['valor'],
          data[this.filaprice[0]][item]['prazo'],
          data[this.filaprice[0]][item]['disponibilidade']
         );
      }
      this.formMdpriceSolo= this.forms.formConstruct('id',this.dataMdprice[0])
      //console.log("formato form:",this.formMdpriceSolo)
      //console.log("formato data:",this.dataMdprice[0])
      //console.log("Md price Duo",data)
      this.dataMdprice.push(new Mdprice());
      for(let item in data[this.filaprice[1]]){
        //console.log(item)
        this.dataMdprice[1].pushItem(
          data[this.filaprice[1]][item]['id'],
          data[this.filaprice[1]][item]['elo'],
          data[this.filaprice[1]][item]['valor'],
          data[this.filaprice[1]][item]['prazo'],
          data[this.filaprice[1]][item]['disponibilidade']
         );
      }
      this.formMdpriceDuo= this.forms.formConstruct('id',this.dataMdprice[1])

      });


      this.tabelaAvulsoprice$ = this.mod.avulsoprice().pipe(
      catchError(error => {
      console.error(error);
      M.toast({html: 'Ops, ocorreu um erro inesperado em carregar Preço Vitória Avulso.'})
      this.isRateLimitReached = true;
      return empty();
      }));
      this.tabelaAvulsoprice$.subscribe(
      data => {
        //console.log("Avulsos Data")
        //console.log(data);
        for(let col in data[this.filaprice[0]][0]){
          this.colsAvulsoSolo.push(col)
        }

        for(let col in data[this.filaprice[1]][0]){
          this.colsAvulsoDuo.push(col)
        }

        //console.log(this.colsAvulsoSolo);

        this.dataAvulsoprice.push(new Avulsoprice());
        //console.log("itens:");
        for(let item in data[this.filaprice[0]]){
          //console.log(item)
          this.dataAvulsoprice[0].pushItem(
            data[this.filaprice[0]][item]['Quant'],
            data[this.filaprice[0]][item]['FERRO'],
            data[this.filaprice[0]][item]['BRONZE'],
            data[this.filaprice[0]][item]['PRATA'],
            data[this.filaprice[0]][item]['OURO'],
            data[this.filaprice[0]][item]['PLATINA'],
            data[this.filaprice[0]][item]['DIAMANTE'],
            data[this.filaprice[0]][item]['MESTRE'],
            data[this.filaprice[0]][item]['prazo'],
            data[this.filaprice[0]][item]['disponibilidade'],
          );
        }

        //console.log(this.dataAvulsoprice[0])
        this.formAvulsopriceSolo= this.forms.formConstruct('Quant',this.dataAvulsoprice[0])
        // console.log("formato form Avulso:",this.formAvulsopriceSolo)
        // console.log("formato data Avulso:",this.dataAvulsoprice[0])

        this.dataAvulsoprice.push(new Avulsoprice());
        for(let item in data[this.filaprice[1]]){
          //console.log(item)
          this.dataAvulsoprice[1].pushItem(
            data[this.filaprice[1]][item]['Quant'],
            data[this.filaprice[1]][item]['FERRO'],
            data[this.filaprice[1]][item]['BRONZE'],
            data[this.filaprice[1]][item]['PRATA'],
            data[this.filaprice[1]][item]['OURO'],
            data[this.filaprice[1]][item]['PLATINA'],
            data[this.filaprice[1]][item]['DIAMANTE'],
            data[this.filaprice[1]][item]['MESTRE'],
            data[this.filaprice[1]][item]['prazo'],
            data[this.filaprice[1]][item]['disponibilidade'],
          );
        }

        this.formAvulsopriceDuo= this.forms.formConstruct('Quant',this.dataAvulsoprice[1])
        //console.log("formato form Avulso Duo:",this.formAvulsopriceSolo)
        //console.log("formato data Avulso Duo:",this.dataAvulsoprice[0])
      }
      );



      this.tabelaBoostsoloprice$ = this.mod.boostsoloprice().pipe(
      catchError(error => {
      console.error(error);
      M.toast({html: 'Ops, ocorreu um erro inesperado em carregar Preço Boost Solo.'})
      this.isRateLimitReached = true;
      return empty();
      }));
      this.tabelaBoostsoloprice$.subscribe(
      data => {
        for (let elo in data[this.filaprice[0]]) {
          this.elosSoloBoost.push(data[this.filaprice[0]][elo]['elo']+' '+data[this.filaprice[0]][elo]['divisao'])
        }
        //console.log(this.elosSoloBoost)

        //console.log("boost solo",data[this.filaprice[0]])
        this.dataBoostSoloprice = new Boostprice();
        for (let item in data[this.filaprice[0]]) {
          this.dataBoostSoloprice.pushItem(
            data[this.filaprice[0]][item]['id'],
            data[this.filaprice[0]][item]['elo'],
            data[this.filaprice[0]][item]['divisao'],
            data[this.filaprice[0]][item]['valor'],
            data[this.filaprice[0]][item]['prazo'],
            data[this.filaprice[0]][item]['disponibilidade']
            )
        }
        //console.log("data boost:",this.dataBoostSoloprice)
        this.formBoostSoloprice= this.forms.formConstruct('id',this.dataBoostSoloprice)
        //console.log("form boost:", this.formBoostSoloprice)
      }
      );

      this.tabelaBoostduoprice$ = this.mod.boostduoprice().pipe(
      catchError(error => {
      console.error(error);
      M.toast({html: 'Ops, ocorreu um erro inesperado em carregar Preço Boost Duo.'})
      this.isRateLimitReached = true;
      return empty();
      }));
      this.tabelaBoostduoprice$.subscribe(
      data => {
        for (let elo in data[this.filaprice[1]]) {
          this.elosDuoBoost.push(data[this.filaprice[1]][elo]['elo']+' '+data[this.filaprice[1]][elo]['divisao'])
        }
        //console.log(this.elosSoloBoost)

        //console.log("boost solo",data[this.filaprice[1]])
        this.dataBoostDuoprice = new Boostprice();
        for (let item in data[this.filaprice[1]]) {
          this.dataBoostDuoprice.pushItem(
            data[this.filaprice[1]][item]['id'],
            data[this.filaprice[1]][item]['elo'],
            data[this.filaprice[1]][item]['divisao'],
            data[this.filaprice[1]][item]['valor'],
            data[this.filaprice[1]][item]['prazo'],
            data[this.filaprice[1]][item]['disponibilidade']
            )
        }
        //console.log("data boost:",this.dataBoostDuoprice)
        this.formBoostDuoprice= this.forms.formConstruct('id',this.dataBoostDuoprice)
        //console.log("form boost:", this.formBoostDuoprice)
      }
      );

    //--------------------------------------------------------------------------
  }

  onSubmitMd(fila,data){
    //console.log(fila, data);
    this.mod.mdpriceUpdate(fila,data);
  }

  onSubmitAvulso(fila,data){
    //console.log(fila, data);
    this.mod.avulsopriceUpdate(fila,data);
  }

  onSubmitBoost(fila,data){
    //console.log(fila, data);
    this.mod.bootspriceUpdate(fila,data);
  }

  onSubmitVariavel(tipo,data){
    this.mod.variaveisUpdate(tipo, data)
  }

}
