import { Router, ActivatedRoute } from '@angular/router';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as moment from "moment";
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private readonly API =  `${environment.API}`;


  constructor(
    private http: HttpClient,
    private router: Router
    ) { }

  checkuser(data){
    return this.http.get(this.API + 'checkuser/' + data);
  }

  // Cliente
  signupClient(data: any){
    return this.http.post(this.API + 'signupClient', data)
  }

  // ADM
  signupAdm(data: any){
    return this.http.post(this.API + 'signupAdm', data).subscribe(console.log);
  }

  // Joober
  signupJobber(data: any){
    console.log(data);
    return this.http.post(this.API + 'signupJobber', data);
  }

  // Login
  signin(data: any){
    return this.http.post<any>(this.API + 'signin', data);
  }

  //Redes sociais
  singinFace(){
    return this.http.get(this.API + 'login/facebook').subscribe(console.log);
  }


  setSession(authResult) {

    const expiresAt = moment().add(30, 'minute');
    localStorage.setItem('username', authResult[1].user.username);
    localStorage.setItem('type', authResult[1].user.type);
    localStorage.setItem('id_token', authResult[0].token.token);
    localStorage.setItem('expires_at', JSON.stringify(expiresAt.valueOf()) );
    this.router.navigate(['']);
  }

  getUsername(){
    return localStorage.getItem("username");
  }

  logout() {
    localStorage.removeItem('username');
    localStorage.removeItem('type');
    localStorage.removeItem('id_token');
    localStorage.removeItem("expires_at");
  }
  public isLoggedIn() {
    return moment().isBefore(this.getExpiration());
  }
  isLoggedOut() {
    return !this.isLoggedIn();
  }

  getExpiration() {
    const expiration = localStorage.getItem("expires_at");
    const expiresAt = JSON.parse(expiration);
    return moment(expiresAt);
  }
}
