import { JobberManageService } from './../../services/jobber-manage.service';
import { Contratacao } from './../../classes/contratacao';
import { AdmManageService } from './../../services/adm-manage.service';

import { Paginator } from './../../classes/paginator';
import { UserManageService } from '../../services/user-manage.service';

import {merge, Observable, of as observableOf} from 'rxjs';
import {catchError, map, startWith, switchMap, filter} from 'rxjs/operators';
import {Component, OnInit, ViewChild, Input} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Servicos } from '../../classes/servicos';

declare var $:any;
declare var M:any;


@Component({
  selector: 'app-bancodejobs',
  templateUrl: './bancodejobs.component.html',
  styleUrls: ['./bancodejobs.component.css']
})
export class BancodejobsComponent implements OnInit {
  displayedColumns: string[] = ['id', 'titulo','username_user', 'prazo', 'valor',  'opcoes'];
  dataSource: MatTableDataSource<Servicos>;
  resultsLength = 0;
  isLoadingResults = false;
  isRateLimitReached = false;

  lista: Servicos[] = [];
  word: string = '';

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(
    private jobber: JobberManageService
  ) {


  }
  ngOnInit(){
    $(document).ready(function(){
      $('.tooltipped').tooltip();
    });
  }

  ngAfterViewInit() {
    this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);
    console.log(this.paginator)
    merge(this.sort.sortChange, this.paginator.page)
      .pipe(
        startWith({}),
        switchMap(() => {
          this.isLoadingResults = false;
          console.log("Aruiq esta o sort");
          console.log(this.paginator)
          console.log(this.sort.active);
          console.log(this.sort.direction);
          return this.jobber.bancoJobs(
            this.sort.active, this.sort.direction, this.paginator
            );
        }),
        map((data: Paginator) => {
          // Flip flag to show that loading has finished.
          this.isLoadingResults = true;
          this.isRateLimitReached = false;
          this.resultsLength = data.total;
          console.log(data.data);
          return data.data;
        }),
        catchError(() => {
          this.isLoadingResults = false;
          // Catch if the GitHub API has reached its rate limit. Return empty data.
          this.isRateLimitReached = true;
          return observableOf([]);
        })
      ).subscribe((data) => this.criarlista(data));

  }

  applyFilter(event: string) {
    this.word = event;
    const filterValue = event;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }


  criarlista(data){
    this.lista = [];
    for (let index = 0; index < data.length; index++) {
      const element = data[index];
      this.lista.push(new Servicos(element['id'],element['titulo'],element['status'],element['prazo'],element['valor'],element['username_user']));

    }

    console.log(this.paginator);
    console.log(this.dataSource);
    this.dataSource = new MatTableDataSource(this.lista);
    console.log(this.paginator);
    console.log(this.dataSource);

    $('#dropdown_a').dropdown();


  }
}
