export class Configjoblanes{
  public lanes: any = {}
  //Configuração de chamadas do BD
  public service_id : number;
  public value = [];
  public campos = [];
  public displayCampos = ["Inferior","Superior", "Central","Suporte","Selva"];
  constructor(data) {
    this.service_id = data['service_id']
    for(let item in data){
      if(item !== 'service_id'){
        this.campos.push(item)
        this.value.push(data[item])
      }
    }
  }

  enviar(data){
    this.value = data;
    this.lanes = {}
    this.lanes["service_id"] = this.service_id
    var i = 0;
    for(let item in this.campos){
      this.lanes[this.campos[item]] = this.value[i]
      i++
    }

    return this.lanes

  }


}
