import { AdmManageService } from 'src/app/arearestrita/services/adm-manage.service';
import { FileManagemService } from './../../../services/file-managem.service';
import { Pagamento } from './../../../classes/pagamento';
import { Contratacao } from './../../../classes/contratacao';
import { Component, OnInit, ViewChild } from '@angular/core';
import { DetalheComponent } from '../../detalhe/detalhe.component';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Observable, empty } from 'rxjs';
import { switchMap, catchError, tap } from 'rxjs/operators';
import { DetalheService } from 'src/app/arearestrita/services/detalhe.service';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { HttpEventType, HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { DomSanitizer } from '@angular/platform-browser';

declare var M:any;
declare var $:any;

@Component({
  selector: 'app-pagamento-job-adm',
  templateUrl: './pagamento-job-adm.component.html',
  styleUrls: ['./pagamento-job-adm.component.css']
})
export class PagamentoJobAdmComponent implements OnInit {

  pagamento$: Observable<Pagamento>;
  contract$: Observable<Contratacao>;
  ref;
  isRateLimitReached =false;



  readonly API =  `${environment.API}`;
  pagamento: Pagamento;

  constructor(
    private route: ActivatedRoute,
    private detalhe: DetalheService,
    private file: FileManagemService,
    private sanitizer: DomSanitizer
  ){
    this.ref = this.route.snapshot.paramMap.get('contract_id');
    console.log(this.ref)




  }
  ngOnInit(): void {

    this.pagamento$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
      { this.ref = params.get('id');
        return this.detalhe.pagamento(this.ref);}
     ),
      catchError(error => {
        //console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado.'})
        this.isRateLimitReached = true;
        $('.collapsible').collapsible();
        return empty();
      }),
      tap(console.log)
    );



    this.contract$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
      { this.ref = params.get('id');
        return this.detalhe.contratacao(this.ref);}
     ),
      catchError(error => {
        //console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado.'})
        this.isRateLimitReached = true;
        $('.collapsible').collapsible();
        return empty();
      }),
      tap(console.log)
    );

    this.pagamento$.subscribe(
      (data)=> {
        this.pagamento = data;
      }
    );




  }

  downloadComprovante(){
    this.file.GetComprovante(this.pagamento.contract_id).subscribe(
      (resp: Blob) =>{
        console.log(resp)
        const file =  new Blob([resp],
          {
            type:resp.type

          }
        );
        const a = document.createElement('a')
        const objectUrl = URL.createObjectURL(file)
        a.href = objectUrl
        a.download = this.pagamento.comprovante.replace('comprovantes/'+'jobber_'+this.pagamento.jobber_username+'/','')
        a.click();
        URL.revokeObjectURL(objectUrl);
      }
    )
  }



}
