import { AdmManageService } from 'src/app/arearestrita/services/adm-manage.service';
import { FileManagemService } from './../../../services/file-managem.service';
import { Pagamento } from './../../../classes/pagamento';
import { Contratacao } from './../../../classes/contratacao';
import { Component, OnInit, ViewChild } from '@angular/core';
import { DetalheComponent } from '../../detalhe/detalhe.component';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Observable, empty } from 'rxjs';
import { switchMap, catchError, tap } from 'rxjs/operators';
import { DetalheService } from 'src/app/arearestrita/services/detalhe.service';
import { FormBuilder, FormControl, Validators } from '@angular/forms';
import { HttpEventType } from '@angular/common/http';

declare var M:any;
declare var $:any;

@Component({
  selector: 'app-pagamento-adm',
  templateUrl: './pagamento-adm.component.html',
  styleUrls: ['./pagamento-adm.component.css']
})
export class PagamentoAdmComponent implements OnInit {

  pagamento$: Observable<Pagamento>;
  contract$: Observable<Contratacao>;
  ref;
  isRateLimitReached =false;

  // Formulário
  formPagamento;
  valorcheio;


  //File Controll
  comprovante:File;
  comprovanteUp = false;
  modoCancelado = false;
  jobber;
  progresso: number = 0;
  contrato: number;


  constructor(
    private route: ActivatedRoute,
    private detalhe: DetalheService,
    private formBuilder: FormBuilder,
    private filecontroller: FileManagemService,
    private adm:AdmManageService
  ){
    this.ref = this.route.snapshot.paramMap.get('contract_id');
    console.log(this.ref)

    this.formPagamento = this.formBuilder.group({
      modo: new FormControl(
        'TOTAL',
        [Validators.required,
        Validators.minLength(3)]
        ),
      valor: new FormControl(
        0,
        [Validators.required]
      ),
      desconto: new FormControl(
        0,
        [Validators.required]
      ),
      comprovante: new FormControl(
        "",
        [Validators.required]
      ),
    });



  }
  ngOnInit(): void {

    this.pagamento$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
      { this.ref = params.get('id');
        return this.detalhe.pagamento(this.ref);}
     ),
      catchError(error => {
        //console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado.'})
        this.isRateLimitReached = true;
        $('.collapsible').collapsible();
        return empty();
      }),
      tap(console.log)
    );



    this.pagamento$.subscribe(
      (data) =>{
        this.formPagamento.controls.valor.setValue(data.valor)

        this.valorcheio = data.valor;
        this.formPagamento.valueChanges.subscribe(form => {
          console.log(form)



        });
      }
    );

    this.contract$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
      { this.ref = params.get('id');
        return this.detalhe.contratacao(this.ref);}
     ),
      catchError(error => {
        //console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado.'})
        this.isRateLimitReached = true;
        $('.collapsible').collapsible();
        return empty();
      }),
      tap(console.log)
    );

    this.contract$.subscribe(
      data => {
        this.formPagamento.controls.desconto.setValue(data.porcentagemFinal-data.porcentagemInicial)
        
        this.jobber = data.jobber_username
        this.contrato = data.id
      }
    )




  }

  updateValor(){
    if(this.formPagamento.value.modo === 'TOTAL')
      this.formPagamento.controls.valor.setValue(this.valorcheio.toFixed(2))
    if(this.formPagamento.value.modo === 'PARCIALMENTE')
      this.formPagamento.controls.valor.setValue((this.valorcheio*(this.formPagamento.value.desconto/100)).toFixed(2))
    if(this.formPagamento.value.modo === 'CANCELADO'){
      this.formPagamento.controls.valor.setValue((0).toFixed(2))
      this.modoCancelado = true;
      console.log(this.modoCancelado)
    }

  }

  onFileChange(event) {
    this.progresso = 0
    this.comprovante = event.target.files[0];
    this.filecontroller.upload(this.comprovante, this.jobber, this.contrato).subscribe(
      resp =>{
        if(resp.type === HttpEventType.Response){
          M.toast({html: 'Comprovante carregado!', classes: 'rounded'});
          this.comprovanteUp= true;
          console.log(resp.body)
          this.formPagamento.controls.comprovante.setValue(resp.body)
        }
        else if (resp.type === HttpEventType.UploadProgress){
          const percentUpload = Math.round((resp.loaded*100)/resp.total);
          console.log(percentUpload)
          this.progresso = percentUpload;
        }

      }

    )
  }


  onSubmit(data) {
    console.log(data)
    data['jobber'] = this.jobber
    data['contract_id']= this.contrato
    this.adm.jobberPagamento(data).subscribe(
      (data)=>{
        M.toast({html: 'Pagamento registrado com sucesso', classes: 'rounded'});
      });
  }


}
