import { Injectable } from '@angular/core';

@Injectable()
export class NavService {


  tipo;

  //rotas
  routesAdm = [
    {route: './', nome: 'Perfil', id:'perfil'},
    {route: './lista-servicos', nome: 'Serviços', id:'servicos'},
    {route: './lista-pagamentos', nome: 'Pagamentos', id:'pagamentos'},
    {route: './lista-clientes', nome: 'Clientes', id:'clientes'},
    {route: './lista-jobbers', nome: 'Jobbers', id:'jobbers'},
    {route: './lista-candidatos', nome: 'Candidadtos', id:'candidatos'},
    {route: './variaveis-globais', nome: 'Varivaies', id:'variaveis'},

  ];

  routesJobbers = [
    {route: './', nome: 'Perfil', id:'perfil'},
    {route: './banco-jobs', nome: 'Banco de Job', id:'bancojob'},
    {route: './lista-servicos', nome: 'Serviços', id:'servicos'},
    {route: './lista-pagamentos', nome: 'Pagamentos', id:'pagamentos'}
  ];

  routesClientes = [
    {route: './', nome: 'Perfil', id:'perfil'},
    {route: './lista-servicos', nome: 'Serviços', id:'servicos'}
  ];

  constructor() {

    this.tipo = localStorage.getItem('type');
    console.log(this.tipo);

  }

  getRotas(){
    if(this.tipo === '0')  return this.routesAdm;
    if(this.tipo === '1')  return this.routesJobbers;
    if(this.tipo === '2')  return this.routesClientes;
    return 'erro';
  }
}
