import { JobberManageService } from './../../../services/jobber-manage.service';
import { UserManageService } from './../../../services/user-manage.service';
import { FormManageService } from './../../../services/form-manage.service';
import { Configjobacesso } from './../../../classes/configJob/configjobacesso';

import { Servicos } from 'src/app/arearestrita/classes/servicos';
import { ClientManageService } from './../../../services/client-manage.service';
import { Observable, empty } from 'rxjs';
import { Component, OnInit, SimpleChanges } from '@angular/core';
import { switchMap, catchError, tap, map } from 'rxjs/operators';
import { ActivatedRoute, ParamMap } from '@angular/router';
import  *  as  data  from  '../../../../../assets/champion.json';
import * as item from '../../../../../assets/item.json'
import * as summoner from '../../../../../assets/summoner.json'
import { ConfigJobManageService } from './../../../services/config-job-manage.service';
import { Configjobchampions } from 'src/app/arearestrita/classes/configJob/configjobchampions';
import { Configjobperiodos } from 'src/app/arearestrita/classes/configJob/configjobperiodos';
import { Configjoblanes } from 'src/app/arearestrita/classes/configJob/configjoblanes';
import { Configjobpartidas } from 'src/app/arearestrita/classes/configJob/configjobpartidas';
import { FormControl, Validators, FormBuilder } from '@angular/forms';

declare var M:any;
declare var $:any;
@Component({
  selector: 'app-servico-principal-jobber',
  templateUrl: './servico-principal-jobber.component.html',
  styleUrls: ['./servico-principal-jobber.component.css']
})
export class ServicoPrincipalJobberComponent implements OnInit {


  dadospartidas = ['CAMPEÃO','RESULTADO','A/M/A','OURO','FEITIÇOS','DATA','MINIONS','ITENS']
  boosting= true
  isRateLimitReached = false;
  isSendPartida = false;

  //Opções sobre o servico
  isFinalite = false;
  motivo = new FormControl('',[Validators.required,Validators.maxLength(120)]);
  service_id;

 //Observaveis
 servico$: Observable<Servicos>;
 champions$: Observable<any>;
 periodos$: Observable<any>;
 acesso$: Observable<any>;
 lanes$: Observable<any>;
 partidas$: Observable<any>;

 //Instancia das configurações
 championsConfig: Configjobchampions;
 periodosConfig: Configjobperiodos;
 acessoConfig: Configjobacesso;
 lanesConfig: Configjoblanes;
 partidasConfig: Configjobpartidas;
 formpartidas;

 //Adicionais adqueridos
 getAcesso = false;
 getCampeoes = false;
 getPeriodo = false;
 getRotas = false;

  //Configurações de tela [CONFIRIR SINCRONIA]
  itensNN = {}
  idpartida;
  datainput;
  tagsitens;
  tagsfeiticos;
  lanesValues = []
  acessoValue = []
  status: boolean;
  ref;
  itens;
  itensName = [];
  itensP = {};
  spell;
  spellName = [];
  spellP = {};
  champions;
  championsName = []
  championsSelected = []
  horariosSelected= Array(7)
  periodos = ["Madrugada","Manhã","Tarde", "Noite", "Ausente"]
  dias = ["Seg", "Ter", "Qua", "Qui", "Sex", "Sab", "Dom"]



  constructor(
    private cliente: ClientManageService,
    private route: ActivatedRoute,
    private configServico: ConfigJobManageService,
    private formBuilder: FormBuilder,
    private user: UserManageService,
    private jobber: JobberManageService
  ) {
    this.champions = data.data;
    this.itens = item.data;
    this.spell = summoner.data;

  //   `service_id` int(10) unsigned NOT NULL,
  // `campeao` varchar(20) NOT NULL,
  // `resultado` varchar(8) NOT NULL,
  // `ama` varchar(15) NOT NULL,
  // `ouro` int(11) NOT NULL,
  // `minions` int(11) NOT NULL,
  // `itens` varchar(254) DEFAULT NULL,
  // `feitiços` varchar(30) NOT NULL,
  // `data` datetime DEFAULT CURRENT_TIMESTAMP

    this.formpartidas = this.formBuilder.group({
      service_id: new FormControl(
        Number(this.route.snapshot.paramMap.get('id')),
        [Validators.required]
        ),
      campeao: new FormControl(
        '',
        [Validators.required]
        ),
      resultado: new FormControl(
        '',
        [Validators.required]
        ),
      ama: new FormControl(
        '',
        [Validators.required]
        ),
      ouro: new FormControl(
        0,
        [Validators.required]
        ),
      minions: new FormControl(
        0,
        [Validators.required]
        ),
      data: new FormControl(
        '',
        [Validators.required]
        )
    });
    console.log(this.itens);

    for (var name in this.champions){
      this.championsName.push(name);
    }

    for (var i in this.itens){
      this.itensName.push(this.itens[i].name);
      this.itensP[this.itens[i].name] = null;
      this.itensNN[this.itens[i].name] = i;
    }

    let spellReplace;
    for (var i in this.spell){
      spellReplace = i.slice(8);
      this.spellName.push(spellReplace);
      this.spellP[spellReplace] = null;
    }


  }



  deletarPartida(id){
    console.log(id)
    this.idpartida = id;
    this.saveChange("historico");
  }

  ngOnChange(){
    var sugestao = this.itensP;
    var spellsugestao = this.spellP;
    M.updateTextFields();
    var elems = document.querySelectorAll('.datepicker');
    this.datainput = M.Datepicker.init(elems, {
      format: 'dd/mm/yy'
    });
    $('.modal').modal();
    $('.chips').chips();
    $('.dropdown-trigger').dropdown();
    $('select').formSelect();
    $('.collapsible').collapsible();
    $('textarea#deixarmotivo,textarea#cancelarmotivo').characterCounter();
    var elemschipsItens = document.querySelectorAll('#chipsItens');
    this.tagsitens = M.Chips.init(elemschipsItens, {
      placeholder: 'Escreva o nome exato do item',
      autocompleteOptions: {

        data: sugestao,
        limit: 2,
        minLength: 0
      }
    });
    var elemschipsFeiticos = document.querySelectorAll('#chipsFeiticos');
    this.tagsfeiticos = M.Chips.init(elemschipsFeiticos, {
      placeholder: 'Escreva o nome exato do item',
      autocompleteOptions: {

        data: spellsugestao,
        limit: 2,
        minLength: 0
      }
    });

  }

  ngOnInit() {
    var sugestao = this.itensP;
    var spellsugestao = this.spellP;
    M.updateTextFields();
    var elemsdatepicker = document.querySelectorAll('.datepicker');
    this.datainput = M.Datepicker.init(elemsdatepicker, {
      format: 'dd/mm/yy'
    });
    $('.modal').modal();
    $('.chips').chips();
    $('.dropdown-trigger').dropdown();
    $('select').formSelect();
    $('.collapsible').collapsible();
    var elemschipsItens = document.querySelectorAll('#chipsItens');
    this.tagsitens = M.Chips.init(elemschipsItens, {
      placeholder: 'Escreva o nome exato do item',
      autocompleteOptions: {

        data: sugestao,
        limit: 2,
        minLength: 0
      }
    });
    var elemschipsFeiticos = document.querySelectorAll('#chipsFeiticos');
    this.tagsfeiticos = M.Chips.init(elemschipsFeiticos, {
      placeholder: 'Escreva o nome exato do item',
      autocompleteOptions: {

        data: spellsugestao,
        limit: 2,
        minLength: 0
      }
    });




    this.servico$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
      { this.ref = params.get('id');
        return this.cliente.service(this.ref);}
     ),
      catchError(error => {
        //console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado.'})
        this.isRateLimitReached = true;
        $('.collapsible').collapsible();
        return empty();
      }),
      tap(console.log)
    );

    this.servico$.subscribe(
      data =>{
        this.service_id= data.id;
        console.log("aqui o progresso",data.progresso)
        if(data.progresso === 100){
          this.isFinalite = true;
          console.log("finalmente", this.isFinalite)
        }
        var tipo = data.tipo
        this.configServico.detalheServicoConfig(data.id, tipo).subscribe(
          (data)=>{
            console.log('aqui',data)
            if(data['lanes']){
              this.getRotas = true
              this.lanes$ = this.route.paramMap.pipe(
                switchMap(params => {
                  return this.configServico.lanes(Number(params.get('id')));
                })
              );

              this.lanes$.subscribe(
                (data) => {
                  this.lanesConfig = new Configjoblanes(data);
                  this.lanesValues = this.lanesConfig.value.slice();
                  console.log("ola",this.lanesConfig)
                }
              )
            }
            if(data['campeos']){
              this.getCampeoes = true
              this.champions$ = this.route.paramMap.pipe(
                switchMap(params => {
                  return this.configServico.listaCampeoes(Number(params.get('id')));
                })
              );

              this.champions$.subscribe(
                (data) => {
                  this.championsConfig = new Configjobchampions(data);
                  this.championsSelected = this.championsConfig.listaChampions;
                }
              )

            }
            if(data['horario']){
              this.getPeriodo = true
              this.periodos$ = this.route.paramMap.pipe(
                switchMap(params => {
                  return this.configServico.periodos(Number(params.get('id')));
                })
              );

              this.periodos$.subscribe(
                (data) => {
                  console.log('o amor é lindo0')
                  this.periodosConfig = new Configjobperiodos(data);
                  this.horariosSelected = this.periodosConfig.listaHorarios;
                }
              )

            }
            if(tipo === 'boostsolo'){
             this.getAcesso = true
              this.acesso$ = this.route.paramMap.pipe(
                switchMap(params => {
                  return this.configServico.acessos(Number(params.get('id')));
                })
              );

              this.acesso$.subscribe(
                (data) => {
                  this.acessoConfig = new Configjobacesso(data);
                  this.acessoValue = this.acessoConfig.value;
                }
              )
            }

            console.log(this.getAcesso)
            console.log(this.getCampeoes)
            console.log(this.getPeriodo)
            console.log(this.getRotas)

            this.ngOnChange()
          }
        )



      }
    )

    this.partidas$ = this.route.paramMap.pipe(
      switchMap(params => {
        return this.configServico.partidas(Number(params.get('id')));
      })
    );

    this. partidas$.subscribe(
      (data) => {
        console.log("partidas", data);
        this.partidasConfig = new Configjobpartidas(data);
        console.log(this.partidasConfig);

      }
    );



  }

  selecionarChampion(champion){
    if(!this.championsSelected.includes(champion) && this.championsSelected.length < 9 )
    this.championsSelected.push(champion);
  }

  removerChampion(champion){
    this.championsSelected = this.arrayRemove(this.championsSelected, champion);
  }

  updateHorarios(data,dia, periodo){
    let temp = this.horariosSelected[dia];
    if(!data){
      temp = this.arrayRemove(temp,periodo)
    }else{
      temp.push(periodo)
    }

    this.horariosSelected[dia]= temp


    if(this.horariosSelected[dia].includes("Ausente"))
      this.horariosSelected[dia] = ["Ausente"]
    if(this.horariosSelected[dia].length === 0)
      this.horariosSelected[dia] = ["Não definido"]
    if(this.horariosSelected[dia].length > 1 && this.horariosSelected[dia].includes("Não definido"))
      this.horariosSelected[dia] = this.arrayRemove(this.horariosSelected[dia],"Não definido")
  }

  arrayRemove(lista, value) {

    return lista.filter(function (ele) {
        return ele !== value;
    });

  }

  chargeacesso(value, i){
    this.acessoValue[i] = value;
  }

  chargelanes(value,i){
    let temp = this.lanesValues.slice()
    temp[i] = value ? 1 : 0;
    this.lanesValues = temp;
  }

  createPartida(data){
    console.log(this.itens)
    let itens = {};

    data['data']=this.datainput.toString();
    let campos = [
      'item1',
      'item2',
      'item3',
      'item4',
      'item5',
      'item6',
      'item7',
    ]

    let i=0;
    if(this.tagsitens[0]['chipsData'])
      for(let item in campos){
        if(i < this.tagsitens[0]['chipsData'].length)
        itens[campos[item]]= this.itensNN[this.tagsitens[0]["chipsData"][i]['tag']];
        i++;
      }
    if(this.tagsfeiticos[0]['chipsData'].length>=1)
    itens['feitico1'] = this.tagsfeiticos[0]["chipsData"][0]['tag'];
      if(this.tagsfeiticos[0]['chipsData'].length>=2)
      itens['feitico2'] = this.tagsfeiticos[0]["chipsData"][1]['tag'];



    console.log(data, itens)
    this.isSendPartida = true;
    this.configServico.createPartida(data,itens).subscribe(()=>{
      this.isSendPartida = false;
      M.toast({html: 'Partida adicionada com sucesso!'})
    });
  }


  saveChange(info){
    if(info === 'champions'){
      let data = this.championsConfig.enviar(this.championsSelected);
      this.configServico.updateChampions(data);
    }

    if(info === 'periodos'){
      let data = this.periodosConfig.enviar(this.horariosSelected);
      this.configServico.updatePeriodos(data);
    }

    if(info === 'acesso'){
      let data = this.acessoConfig.enviar(this.acessoValue);
      this.configServico.updateAcesso(data);
    }

    if(info === 'lanes'){
      let data = this.lanesConfig.enviar(this.lanesValues);
      this.configServico.updateLanes(data);
    }

    if(info === 'historico'){
      this.configServico.deletePartidas(this.idpartida);
    }

  }

  updateProgresso(progresso){
    if(progresso === 100){
      this.isFinalite = true;
      console.log("finalmente", this.isFinalite)
    }
    this.configServico.updateProgresso(progresso,this.route.snapshot.paramMap.get('id'), this.user.getUser());
  }


  //Critical ações
  cancelarServico(){
    console.log("cancelar")
    console.log(this.motivo.value)
    console.log(this.service_id)
    console.log(this.user.getUser())
    this.jobber.cancelarServico(this.motivo.value,this.service_id,this.user.getUser()).subscribe(console.log)
  }
  deixarServico(){
    console.log("deixar")
    console.log(this.motivo.value)
    this.jobber.deixarServico(this.motivo.value,this.service_id,this.user.getUser()).subscribe(console.log)
  }
  finalizarServico(){
    console.log("finalizar")
    this.configServico.finalizarServico(Number(this.route.snapshot.paramMap.get('id')),"jobber")
  }


}
