import { FormControl, FormGroup } from '@angular/forms';
import { Mdprice } from './../classes/prices/mdprice';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class FormManageService {

  objetoteste = {};
  constructor() { }

  formConstruct(pk,tabela){
    console.log(tabela)
    let group: any = {};
    tabela.tabela.forEach(
      (item) => {
        console.log(item)
        this.objetoteste = {}
        for (let teste in item) {
          this.objetoteste[teste]=new FormControl(item[teste])
        }
        console.log(this.objetoteste)
        group[item[pk]] = new FormGroup(this.objetoteste)
      }
    );
    return new FormGroup(group);

  }
}
