import { PagamentoJobAdmComponent } from './detalhes/jobber/pagamento-job-adm/pagamento-job-adm.component';
import { ListapagamentojobberComponent } from './jobber/listapagamentojobber/listapagamentojobber.component';
import { PagamentoAdmComponent } from './detalhes/adm/pagamento-adm/pagamento-adm.component';
import { ServicoPrincipalAdmComponent } from './detalhes/adm/servico-principal-adm/servico-principal-adm.component';
import { ServicoPrincipalJobberComponent } from './detalhes/jobber/servico-principal-jobber/servico-principal-jobber.component';
import { ServicoPrincipalComponent } from './detalhes/cliente/servico-principal/servico-principal.component';
import { PerfilCandidatoDetalheComponent } from './detalhes/adm/perfil-candidato-detalhe/perfil-candidato-detalhe.component';
import { ListagemComponent } from './listagem/listagem.component';
import { BancodejobsComponent } from './jobber/bancodejobs/bancodejobs.component';
import { ModificarvariaveisComponent } from './adm/modificarvariaveis/modificarvariaveis.component';
import { ListacondidatosComponent } from './adm/listacondidatos/listacondidatos.component';
import { ListaservicosjobberComponent } from './jobber/listaservicosjobber/listaservicosjobber.component';
import { ListajobbersComponent } from './adm/listajobbers/listajobbers.component';
import { ListaclientesComponent } from './adm/listaclientes/listaclientes.component';
import { ListaservicosadmComponent } from './adm/listaservicosadm/listaservicosadm.component';
import { ListapagamentosComponent } from './adm/listapagamentos/listapagamentos.component';
import { ProfileComponent } from './profile/profile.component';
import { ListaservicosclienteComponent } from './cliente/listaservicoscliente/listaservicoscliente.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Meus Componentes
import { BashboardComponent } from './bashboard/bashboard.component';
import { AuthGuard } from './../auth/auth.guard';
import { PerfilDetalheComponent } from './detalhes/adm/perfil-detalhe/perfil-detalhe.component';
import { CandidatoPerfilComponent } from './detalhes/adm/candidato-perfil/candidato-perfil.component';
import { JobDetalheComponent } from './detalhes/jobber/job-detalhe/job-detalhe.component';
import { PerfilJobberDetalheComponent } from './detalhes/adm/perfil-jobber-detalhe/perfil-jobber-detalhe.component';
import { ServicoDetalheComponent } from './detalhes/adm/servico-detalhe/servico-detalhe.component';

const routes: Routes = [
  {
    path: 'cliente',
    component: BashboardComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: '',
        component: ProfileComponent
      },
      {
        path: 'lista-servicos',
        component: ListaservicosclienteComponent
      },
      {
        path: 'servico/:id',
        component: ServicoPrincipalComponent
      }
    ]
  },
  {
    path: 'adm',
    component: BashboardComponent,
    canActivate: [AuthGuard],
    children: [

      {
        path: 'lista-pagamentos',
        component: ListapagamentosComponent
      },
      {
        path: 'lista-pagamentos/pagamento/:id',
        component: PagamentoAdmComponent
      },
      {
        path: 'lista-servicos',
        component: ListaservicosadmComponent
      },
      {
        path: 'lista-servicos/servico/:id',
        component: ServicoPrincipalAdmComponent
      },
      {
        path: 'lista-clientes',
        component: ListaclientesComponent
      },
      {
        path: 'lista-clientes/cliente/:username',
        component: PerfilDetalheComponent
      },

      {
        path: 'lista-clientes/cliente/servico/:id',
        component: ServicoPrincipalAdmComponent
      },
      {
        path: 'lista-jobbers',
        component: ListajobbersComponent
      },
      {
        path: 'lista-jobbers/jobber/:username',
        component: PerfilJobberDetalheComponent
      },
      {
        path: 'lista-jobbers/jobber/servico/:id',
        component: ServicoPrincipalAdmComponent
      },
      {
        path: 'lista-jobbers/candidato/:username',
        component: PerfilCandidatoDetalheComponent
      },
      {
        path: 'lista-candidatos',
        component: ListacondidatosComponent
      },
      {
        path: 'lista-candidatos/candidato/:username',
        component: CandidatoPerfilComponent
      },
      {
        path: 'variaveis-globais',
        component: ModificarvariaveisComponent
      },

      {
        path: '',
        component: ProfileComponent
      }
    ]
  },
  {
    path: 'jobber',
    component: BashboardComponent,
    canActivate: [AuthGuard],
    children: [
      {
        path: '',
        component: ProfileComponent
      },
      {
        path: 'lista-pagamentos',
        component: ListapagamentojobberComponent
      },
      {
        path: 'lista-pagamentos/pagamento/:id',
        component: PagamentoJobAdmComponent
      },
      {
        path: 'lista-servicos',
        component: ListaservicosjobberComponent
      },
      {
        path: 'servico/:id',
        component: ServicoPrincipalJobberComponent
      },
      {
        path: 'banco-jobs',
        component: BancodejobsComponent
      },
      {
        path: 'job/:id',
        component: JobDetalheComponent
      },

    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RestritoRoutingModule {}
