
export class Mdprice {
  public tabela: { id: number, elo: string, valor: number, prazo: number, disponibilidade: number }[] = []
  constructor() { }
  pushItem(id: number, elo: string, valor: number, prazo: number, disponibilidade: number) {
    this.tabela.push({ id, elo, valor, prazo, disponibilidade })
  }
}
