import { Router } from '@angular/router';
import { Compra } from './../classes/compra';
import { CompraService } from './../services/compra.service';

import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';

declare var paypal: any;

var comprar //Variavel global para repasse do serviço comprar


@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {

  @ViewChild('paypal', {static:true}) paypalElement: ElementRef;

  // Visualização
  viewPedido;

  constructor(
    private compra: CompraService,
    private router: Router
  ) {
    console.log(this.compra.pedido);
    if(!this.compra.pedido){
      this.router.navigate([""]);
    }
    comprar = this.compra
    this.viewPedido = this.compra.pedido;

  }





  ngOnInit() {
    // this.viewPedido.valor = ""+(this.viewPedido.valor.toFixed(2)).replace('.', ',');
    const valor = this.viewPedido.valor;
    console.log(valor);
    this.viewPedido.valor = (''+parseFloat(valor).toFixed(2)).replace('.', ',');
    const prazo = this.viewPedido.prazo;

    const titulo = this.viewPedido.titulo;

    paypal.Buttons({
      createOrder: function(data, actions) {
        // This function sets up the details of the transaction, including the amount and line item details.
        return actions.order.create({
          purchase_units: [{
            description: titulo + " , prazo: " + prazo,
            amount: {
              currency_code: 'BRL',
              value: parseFloat(valor).toFixed(2)
            }
          }]
        });
      },
      onApprove: function(data, actions) {
        // This function captures the funds from the transaction.
        return actions.order.capture().then(function(details) {
          // This function shows a transaction success message to your buyer.
          console.log(comprar)
          comprar.fCompraTipo().subscribe(dados => {
            console.log(dados);
          });
          alert('Transaction completed by ' + details.payer.name.given_name);
        });
      }
    }).render(this.paypalElement.nativeElement);
  }

}
