import * as moment from "moment";
export class Contratacao {
  constructor(
      public id: number,
      public username_user: string,
      public jobber_username: string,
      public titulo: string,
      public status: string,
      public prazo: number,
      public service_id?: number,
      public valor?: any,
      public atribuido?: boolean,
      public created_at?: string,
      public prazojob?: string,
      public porcentagemInicial?: number,
      public porcentagemFinal?: number,
      public porcentagem?: number,
      public progressActive?: boolean
    ) {
      if(this.valor) this.valor = this.valor.toFixed(2);
      if(this.jobber_username) this.atribuido = true;
      else this.atribuido = false;

      if(this.created_at){
      this.prazojob = moment(this.created_at).subtract(this.prazo, 'days').fromNow()
        console.log(moment(this.created_at))
      }

      this.porcentagem = this.porcentagemFinal-this.porcentagemInicial
      this.progressActive  =true;
     }
}
