import { Perfil } from './../classes/Perfil';
import { UserManageService } from './../services/user-manage.service';
import { Component, OnInit } from '@angular/core';
import { Observable, empty } from 'rxjs';
import { FormBuilder, FormControl } from '@angular/forms';
import { AuthService } from 'src/app/auth/auth.service';
import { NavService } from 'src/app/public/services/nav.service';
import { catchError } from 'rxjs/operators';

declare var M:any;

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  //Conter mudanças
  profileupdate;
  //Carregar dados
  profile$: Observable<Perfil>;
  error= false;

  constructor(
    private user: UserManageService,
    private formBuilder: FormBuilder,
    private auth: AuthService,
    private nav: NavService
    ) {
      this.profile$ = this.user.profile(this.user.getUser()).pipe(
        catchError(error => {
          console.error(error);
          M.toast({html: 'Ops, ocorreu um erro inesperado.'})
          this.error = true;
          return empty();
        })
      )
      this.profile$.subscribe(
        (data) =>{
          this.profileupdate = this.formBuilder.group({
            firstName: new FormControl(data['firstName']),
            lastName: new FormControl(data['lastName']),
            address: new FormControl(data['address']),
            username: new FormControl(data['username']),
            contact: new FormControl(data['contact'])

          });
        }
      );

   }


  ngOnInit() {

  }

  onSubmit(data: Perfil) {
    console.log(data);
    if (this.auth.isLoggedIn()) {
      this.user.profileUpdate(this.user.getUser(),data);
    } else {
      this.nav.onOpen();
    }
  }

}
