import { Pagamento } from './../classes/pagamento';
import { element } from 'protractor';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { Jobbers } from '../classes/jobbers';
import { Candidato } from '../classes/candidato';
import { Cliente } from '../classes/clientes';
import { Contratacao } from '../classes/contratacao';
import { Servicos } from '../classes/servicos';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ListagemService {

  private readonly API =  `${environment.API}`;


  constructor(private auth: AuthService, private http: HttpClient) { }

  listar(o, d, p, r, rota){
    console.log(rota);
    console.log(r);
    switch (rota) {
      case 'admJobbers':
        return this.listaJobbers(o,d,p);
      case 'admCandidatos':
        return this.listaCandidatos(o,d,p);
      case 'admClientes':
        return this.listaClientes(o,d,p);

      case 'admServicos':
        return this.listaServicos(o,d,p);

      case 'admServicosCliente':
        return this.listaServicosCliente(o,d,p,r);

      case 'admServicosJobber':
        return this.listaServicosJobber(o,d,p,r);

      case 'clienteServicos':
        return this.ClientelistaServicos(o,d,p,r);

      case 'jobberServicos':
        return this.jobberlistaServicos(o,d,p,r);

      case 'admPagamentos':
        return this.listaPagamentoJobber(o,d,p,r);

      case 'jobberPagamentos':
          return this.JobberlistaPagamentoJobber(o,d,p,r);

      case 'admContratosServico':
        return this.listaContratoServico(o,d,p,r);
      default:
        break;
    }
  }

  data(element,rota){
    console.log(element, rota)
    switch (rota) {
      case 'admJobbers':
        return new Jobbers(element['user_username'],element['firstName'],element['lastName'],element['progresso'],element['created_at'],element['status'])

      case 'admCandidatos':
        return new Candidato(element['user_username'],element['firstName'],element['lastName'],element['created_at'],element['status'])

      case 'admClientes':
        return new Cliente(element['user_username'],element['firstName'],element['lastName'],element['updated_at'],element['status'])

      case 'admServicos':
        return new Contratacao(element['id'],element['username_user'],element['jobber_username'],element['titulo'],element['status'],element['prazo'])

      case 'admServicosCliente':
        return new Contratacao(element['id'],element['username_user'],element['jobber_username'],element['titulo'],element['status'],element['prazo'], null,null, element['created_at'])

      case 'admServicosJobber':
        return new Servicos(element['id'],element['titulo'],null,element['prazo'],null,element['username_user'],null,element['progresso'],element['created_at'],null,true);

      case 'clienteServicos':
        return new Servicos(element['id'],element['titulo'],element['status'],element['prazo'],element['valor'])

      case 'jobberServicos':
        return new Contratacao(element['service_id'],element['username_user'],element['jobber_username'],element['titulo'],element['status'],element['prazo'],element['valor'],null,element['created_at'])

      case 'admPagamentos':
        return new Pagamento(element['comprovante'],element['contract_id'],element['jobber_username'],element['status'],element['statusPago'],element['tipo'],element['titulo'],element['valor'],element['valorPago'],element['service_id'])

      case 'jobberPagamentos':
        return new Pagamento(element['comprovante'],element['contract_id'],element['jobber_username'],element['status'],element['statusPago'],element['tipo'],element['titulo'],element['valor'],element['valorPago'])

      case 'admContratosServico':
        return new Contratacao(element['id'],'',element['jobber_username'],'',element['status'],0, 0,null,null,null,null, element['porcentagemInicial'], element['porcentagemFinal'])


      default:
        break;
    }
  }

  //Lista Jobbers - ADM
  listaJobbers(ordem,direcao,paginator){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'listaJobbers/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao);
    }
  }

  //Lista Candidados - ADM
  listaCandidatos(ordem,direcao,paginator){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'listaCandidatos/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao);
    }
  }

  //Lista Clientes - ADM
  listaClientes(ordem,direcao,paginator){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'listaClientes/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao);
    }
  }

  //Lista Servicos - ADM
  listaServicos(ordem,direcao,paginator){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'adm/listaServicos/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao);
    }
  }

  //
  listaServicosCliente(ordem,direcao,paginator, username){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'adm/listaServicosCliente/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao+'/'+username);
    }
  }

  //
  listaServicosJobber(ordem,direcao,paginator,username){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'adm/listaServicosJobber/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao+'/'+username);
    }
  }

  //Lista Pagamentos - Adm
  listaPagamentoJobber(ordem,direcao,paginator,username){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'adm/listaPagamento/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao+'/'+username);
    }
  }

  //Lista Pagamentos - Jobber
  JobberlistaPagamentoJobber(ordem,direcao,paginator,username){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'jobber/listaPagamento/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao+'/'+username);
    }
  }

  //Lista Servico - Cliente
  ClientelistaServicos(ordem,direcao,paginator, username){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'listaServicos/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao+'/'+username);
    }
  }

  //Lista Servico - Jobber
  jobberlistaServicos(ordem,direcao,paginator,username){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
     return this.http.get(this.API + 'jobber/listaServicos/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao+'/'+username);
    }
  }

  //Lista Contratos em Servico - ADM
  listaContratoServico(ordem,direcao,paginator,service_id){
    const page = paginator.pageIndex + 1;
    if(this.auth.isLoggedIn()){
      return this.http.get(this.API + 'adm/listaContratosServicos/'+page+'/'+paginator.pageSize+'/'+ordem+'/'+direcao+'/'+service_id);
    }
  }
}
