import { NavService } from './../services/nav.service';
import { BoostDuo } from './../classes/boostduo';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { AuthService } from 'src/app/auth/auth.service';
import { PricesService } from '../services/prices.service';
import { CompraService } from '../services/compra.service';

declare var $:any;

@Component({
  selector: 'app-boostduo',
  templateUrl: './boostduo.component.html',
  styleUrls: ['./boostduo.component.css']
})
export class BoostduoComponent implements OnInit {
//Consulta de preço
priceDuo = [];

//Feedback compra
total;
totalview; //GAmbi para corrijir virgulae não conflitar com pagamento
prazo;
disabled;

//Formulario
compra;
pedido: BoostDuo;

//[Integração] Consultar diretrizes do site
divisoes = [] ;
filas = ['SOLO/DUO', 'FLEX'];

//Multiplicados do total
coach;

//////////client-side-server///////////////

constructor(
  private formBuilder: FormBuilder,
  private auth: AuthService,
  private price: PricesService,
  private comprar: CompraService,
  private nav: NavService
) {
  this.compra = this.formBuilder.group({
    elo_atual: ['FERRO 4', Validators.required],
    fila: ['SOLO/DUO', Validators.required],
    pdls: [15, Validators.required],
    elo_final: ['PRATA 4', Validators.required],
    lanes: [true, Validators.required],
    horario: [true, Validators.required],
    junto: [true, Validators.required],
    coach: [false, Validators.required],
  });
}

ngOnInit() {

  $(document).ready(function(){
    $('select').formSelect();
  });


   //[Servidor] Comunição servidor price
   this.price.boostduoprice().subscribe(dados => {
    console.log(dados);
    //Conferir a ordem dos preços
    this.config(dados);
    //Inicializar valor
    this.valorLive(this.compra.value);
  });

  //Instancia de observable change form
  this.onChanges();

}

config(dados) {
  console.log(dados);
  for (let index = 0; index < dados['priceDuo'].length; index++) {
    if(dados['priceDuo'][index]['disponibilidade']){

      //Select Disponivel
      if(dados['priceDuo'][index]['visivel'])
        this.divisoes.push(dados['priceDuo'][index]['elo'] + ' ' + dados['priceDuo'][index]['divisao']);
      // Prices
      this.priceDuo.push([dados['priceDuo'][index]['elo'] + ' ' + dados['priceDuo'][index]['divisao'],dados['priceDuo'][index]['valor'],dados['priceDuo'][index]['prazo']]);
    }
  }

  //Sincronizar com BD, devido a ordem
  dados['variaveisglobais'].forEach((item) => {
    console.log(item.variaveis);
    if(item.variaveis === 'coach')
      this.coach = item.valor

  });
  console.log(this.coach);

  console.log(this.priceDuo);
}

onChanges(): void {
  this.compra.valueChanges.subscribe(form => {
    console.log(form)
    this.valorLive(form);
  });
}

valorLive(form) {
  console.log(form);
  var somaTotal = 0;
  var somaPrazo = 0
  var flag = false;
  for (let index = 0; index < this.priceDuo.length; index++) {
    if(flag){
      somaPrazo = somaPrazo +  this.priceDuo[index][2];
      somaTotal = somaTotal +  this.priceDuo[index][1];

    }
    if(this.priceDuo[index][0] === form.elo_atual) flag = true;
    if(this.priceDuo[index][0] === form.elo_final) {
      flag = false;
      index = this.priceDuo.length; //encerrar loop
    }
  }

  this.total = somaTotal;
  this.prazo = somaPrazo;

 // Adicionais muliplixadores
 let total_limpo = this.total;

 if(form.coach) this.total= this.total+this.acrecimo(total_limpo,this.coach);
 this.totalview = ""+(this.total.toFixed(2)).replace('.', ',');
 this.total = parseFloat(this.total.toFixed(2));
 console.log(this.total);
 console.log(this.totalview);
 if(this.total === 0 ) this.disabled = true;
 else this.disabled = false;
}

onSubmit(data) {
 if (this.auth.isLoggedIn()) {
   this.pedido = new BoostDuo(
     data["elo_atual"],
     data["fila"],
     data["pdls"],
     data["elo_final"],
     data['junto'],
     data["lanes"],
     data["horario"],
     data["coach"],
     this.total,
     this.prazo);
   console.log(this.pedido);
   this.comprar.compraBoostDuo(this.pedido);
 } else {
   this.nav.onOpen();
 }
}

acrecimo(total,porc){
 return total*(porc/100);
}

desconto(total,porc){
 return -total*(porc/100);
}


}
