import { AdmManageService } from './../../services/adm-manage.service';
import { DetalheService } from './../../services/detalhe.service';
import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Observable, empty } from 'rxjs';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { switchMap, catchError, tap } from 'rxjs/operators';

declare var M: any;

@Component({
  selector: 'app-detalhe',
  templateUrl: './detalhe.component.html',
  styleUrls: ['./detalhe.component.css']
})
export class DetalheComponent implements OnInit {
  // ---------------------------------------------------- Orientado a DADOS ------------------------------------------------------------

  @Input() descricaoAcao: string;

  @Input() acaoTitulo: string[];

  @Input() sessions: [];
  @Input() titleSessions: [];

  @Input() data: string;

  @Input() rota: string;

  @Input() interface$;

  @Output() emitir = new EventEmitter<string>();

  // ---------------------------------------------------- Orientado a DADOS ------------------------------------------------------------
  isAcoes = false;
  isRateLimitReached = false;
  profile$: Observable<any>;
  status: boolean;
  ref;

  constructor(
    private route: ActivatedRoute,
    private detalhe: DetalheService,
    private adm: AdmManageService
    ) {


   }
  ngOnInit(): void {
    console.log(this.acaoTitulo.length);
    if(this.acaoTitulo.length) this.isAcoes = true;
    this.profile$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
      { this.ref = params.get(this.data);
        return this.detalhe.detalhar(this.ref,this.rota);}
     ),
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado.'})
        this.isRateLimitReached = true;
        return empty();
      }),
      tap(console.log)
    );
  }

  emitirReacao(reacao: string){
    console.log(reacao)
    this.emitir.emit(reacao);
  }

}
