import { Contratacao } from './../classes/contratacao';
import { Servicos } from './../classes/servicos';
import { ListagemService } from './../services/listagem.service';
import {merge, of as observableOf} from 'rxjs';
import {catchError, map, startWith, switchMap, filter} from 'rxjs/operators';
import {Component, OnInit, ViewChild, Input} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Paginator } from '../classes/paginator';
import { ParamMap, ActivatedRoute } from '@angular/router';
import { Pagamento } from '../classes/pagamento';
import { Jobbers } from '../classes/jobbers';
import { Candidato } from '../classes/candidato';
import { Cliente } from '../classes/clientes';

declare var $:any;
declare var M:any;
@Component({
  selector: 'app-listagem',
  templateUrl: './listagem.component.html',
  styleUrls: ['./listagem.component.css']
})
export class ListagemComponent implements OnInit {

  @Input() paramRoute: boolean ;
  @Input() activeFilter: boolean ;
  @Input() displayedColumns: string[] ;
  @Input() columnsDefinies: string[] ;
  @Input() columnsDisplay: string[] ;
  @Input() columnsAttr: any[any] ;
  @Input() ref: any;
  @Input() rota: string ;

  columnsToDisplay: string[]
  dataSource: MatTableDataSource<Servicos>;
  resultsLength = 0;
  isLoadingResults = false;
  isRateLimitReached = false;

  lista: any[] = [];
  word: string = '';

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;

  constructor(
    private listagem : ListagemService,
    private route: ActivatedRoute
  ) {


  }
  ngOnInit(){
    $(document).ready(function(){
      $('.tooltipped').tooltip();
    });
    this.columnsToDisplay = this.displayedColumns.slice();
    console.log(this.ref)
    if(this.paramRoute)
      this.ref = this.route.snapshot.paramMap.get(this.ref);

    console.log(this.ref);
  }

  ngAfterViewInit() {
    console.log(this.ref,this.rota)
    console.log(this.columnsAttr);
    this.sort.sortChange.subscribe(() => this.paginator.pageIndex = 0);
    console.log(this.paginator)
    merge(this.sort.sortChange, this.paginator.page)
      .pipe(
        startWith({}),
        switchMap(() => {
          this.isLoadingResults = false;
          console.log(this.paginator)
          console.log(this.sort.active);
          console.log(this.sort.direction);
          return this.listagem.listar(
            this.sort.active, this.sort.direction, this.paginator,this.ref,this.rota
          );
        }),
        map((data: Paginator) => {
          // Flip flag to show that loading has finished.
          this.isLoadingResults = true;
          this.isRateLimitReached = false;
          this.resultsLength = data.total;
          console.log(data.data);
          return data.data;
        }),
        catchError(() => {
          this.isLoadingResults = false;
          // Catch if the GitHub API has reached its rate limit. Return empty data.
          this.isRateLimitReached = true;
          return observableOf([]);
        })
      ).subscribe((data) =>{
        this.criarlista(data);
        console.log("DAta bruta", data);
      } );

  }

  applyFilter(event: string) {
    this.word = event;
    const filterValue = event;
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }


  criarlista(data){
    console.log("dados brutos", data)
    this.lista = [];
    for (let index = 0; index < data.length; index++) {
      const element = data[index];
      this.lista.push(this.listagem.data(element, this.rota))
    }


    this.dataSource = new MatTableDataSource(this.lista);
    console.log(this.paginator);
    console.log(this.dataSource);

    $('#dropdown_a').dropdown();


  }
}

