
export class Avulsoprice {
  public tabela: {
    Quant: number,
    FERRO: string,
    BRONZE: string,
    PRATA: string,
    OURO: string,
    PLATINA: string,
    DIAMANTE: string,
    MESTRE: string,
    prazo: number,
    disponibilidade: number
  }[] = []
  constructor() { }
  pushItem(
    Quant: number,
    FERRO: string,
    BRONZE: string,
    PRATA: string,
    OURO: string,
    PLATINA: string,
    DIAMANTE: string,
    MESTRE: string,
    prazo: number,
    disponibilidade: number
    ) {
    this.tabela.push({
    Quant,
    FERRO,
    BRONZE,
    PRATA,
    OURO,
    PLATINA,
    DIAMANTE,
    MESTRE,
    prazo,
    disponibilidade
    })
  }
}
