import { Injectable } from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ConfigJobManageService {

  private readonly API =  `${environment.API}`;

  constructor(private auth: AuthService, private http: HttpClient) { }

  //GET - PARAMETROS-------------------------------
  //Campeoes

  detalheServicoConfig(service_id,tipo){
    console.log("ID antes reqs",service_id)
    if(this.auth.isLoggedIn) {
      return this.http.get(this.API + 'config-job/detalhes/' + service_id+ '/'+ tipo);
    }
  }

  listaCampeoes (service_id) {
    console.log("ID antes reqs",service_id)
    if(this.auth.isLoggedIn) {
      return this.http.get(this.API + 'config-job/listaCampeoes/' + service_id);
    }
  }

  periodos (service_id) {
    console.log("ID antes reqs",service_id)
    if(this.auth.isLoggedIn) {
      return this.http.get(this.API + 'config-job/periodos/' + service_id);
    }
  }

  acessos (service_id) {
    console.log("ID antes reqs",service_id)
    if(this.auth.isLoggedIn) {
      return this.http.get(this.API + 'config-job/acessos/' + service_id);
    }
  }

  lanes (service_id) {
    console.log("ID antes reqs",service_id)
    if(this.auth.isLoggedIn) {
      return this.http.get(this.API + 'config-job/lanes/' + service_id);
    }
  }

  partidas (service_id) {
    console.log("ID antes reqs",service_id)
    if(this.auth.isLoggedIn) {
      return this.http.get(this.API + 'config-job/partidas/' + service_id);
    }
  }


  //-----------------------------------------------
  //PUT - PARAMETROS-------------------------------

  updateChampions(data) {

      if(this.auth.isLoggedIn())
        return this.http.put(this.API + 'updateChampions', {data}).subscribe(console.log);

  }

  updatePeriodos(data) {

      if(this.auth.isLoggedIn())
        return this.http.put(this.API + 'updatePeriodos', {data}).subscribe(console.log);

  }

  updateAcesso(data) {

    if(this.auth.isLoggedIn())
      return this.http.put(this.API + 'updateAcesso', {data}).subscribe(console.log);

  }

  updateLanes(data) {

    if(this.auth.isLoggedIn())
      return this.http.put(this.API + 'updateLanes', {data}).subscribe(console.log);

  }

  updateProgresso(progresso,service_id, jobber){
    if(this.auth.isLoggedIn())
      return this.http.put(this.API + 'updateProgresso', {progresso, service_id, jobber}).subscribe(console.log);
  }

  createPartida(data, itens){
    console.log(data,itens);
    if(this.auth.isLoggedIn())
      return this.http.post(this.API + 'createPartida', {data,itens});
  }

  deletePartidas(id) {

    if(this.auth.isLoggedIn) {
      console.log(id)
      return this.http.post(this.API + 'deletePartidas', {id}).subscribe(console.log);
    }
  }


  // AREA CRITICA
  finalizarServico(id,checkfrom){
    if(this.auth.isLoggedIn) {
      console.log(id)
      if(checkfrom === 'jobber')
      return this.http.post(this.API + 'jobber/critical/finalizar', {id}).subscribe(console.log);
      if(checkfrom === 'cliente')
      return this.http.post(this.API + 'cliente/critical/finalizar', {id}).subscribe(console.log);
    }
  }
}
