import { Variaveis } from './../../arearestrita/classes/prices/variaveis';
import { BoostSolo } from '../classes/boostsolo';
import { CompraService } from '../services/compra.service';
import { Component, OnInit } from '@angular/core';
import { PricesService } from '../services/prices.service';
import { AuthService } from 'src/app/auth/auth.service';
import { FormBuilder, Validators } from '@angular/forms';
import { NavService } from '../services/nav.service';

declare var $: any;

@Component({
  selector: 'app-boost-solo',
  templateUrl: './boostsolo.component.html',
  styleUrls: ['./boostsolo.component.css']
})
export class BoostSoloComponent implements OnInit {

  //Consulta de preço
  priceSolo = [];

  //Feedback compra
  total;
  totalview; //GAmbi para corrijir virgulae não conflitar com pagamento
  prazo;
  disabled;

  //Formulario
  compra;
  pedido: BoostSolo;

  //[Integração] Consultar diretrizes do site
  divisoes = [] ;
  filas = ['SOLO/DUO', 'FLEX'];

  //Multiplicados do total
  campeoes;
  horarios;
  kda;
  stream;

  //////////client-side-server///////////////

  constructor(
    private formBuilder: FormBuilder,
    private auth: AuthService,
    private price: PricesService,
    private comprar: CompraService,
    private nav: NavService
  ) {
    this.compra = this.formBuilder.group({
      elo_atual: ['FERRO 4', Validators.required],
      fila: ['SOLO/DUO', Validators.required],
      pdls: [15, Validators.required],
      elo_final: ['PRATA 4', Validators.required],
      tecla: [true, Validators.required],
      chat: [true, Validators.required],
      lanes: [true, Validators.required],
      campeoes: [false, Validators.required],
      horario: [false, Validators.required],
    });
  }

  ngOnInit() {

    $(document).ready(function(){
      $('select').formSelect();
    });


     //[Servidor] Comunição servidor price
     this.price.boostsoloprice().subscribe(dados => {
      console.log(dados);
      //Conferir a ordem dos preços
      this.config(dados);
      //Inicializar valor
      this.valorLive(this.compra.value);
    });

    //Instancia de observable change form
    this.onChanges();

  }

  config(dados) {
    console.log(dados);
    for (let index = 0; index < dados['priceSolo'].length; index++) {
      if(dados['priceSolo'][index]['disponibilidade']){

        //Select Disponivel
        if(dados['priceSolo'][index]['visivel'])
          this.divisoes.push(dados['priceSolo'][index]['elo'] + ' ' + dados['priceSolo'][index]['divisao']);
        // Prices
        this.priceSolo.push([dados['priceSolo'][index]['elo'] + ' ' + dados['priceSolo'][index]['divisao'],dados['priceSolo'][index]['valor'],dados['priceSolo'][index]['prazo']]);
      }
    }

    //Sincronizar com BD, devido a ordem
    dados['variaveisglobais'].forEach((item) => {
      console.log(item.variaveis);
      if(item.variaveis === 'campeoes')
        this.campeoes = item.valor
      if(item.variaveis === 'horarios')
        this.horarios = item.valor

    });
  }

  onChanges(): void {
    this.compra.valueChanges.subscribe(form => {
      console.log(form)
      this.valorLive(form);
    });
  }

  valorLive(form) {
    console.log(form);
    var somaTotal = 0;
    var somaPrazo = 0;
    var flag = false;
    for (let index = 0; index < this.priceSolo.length; index++) {
      if(flag){
        somaPrazo = somaPrazo +  this.priceSolo[index][2];
        somaTotal = somaTotal +  this.priceSolo[index][1];

      }
      if(this.priceSolo[index][0] === form.elo_atual) flag = true;
      if(this.priceSolo[index][0] === form.elo_final) {
        flag = false;
        index = this.priceSolo.length; //encerrar loop
      }
    }

    this.total = somaTotal;
    this.prazo = somaPrazo;

    // Adicionais muliplixadores
    let total_limpo = this.total;

    if(form.campeoes) this.total= this.total+this.acrecimo(total_limpo,this.campeoes);
    if(form.horario) this.total= this.total+this.acrecimo(total_limpo,this.horarios);
    this.totalview = ""+(this.total.toFixed(2)).replace('.', ',');
    this.total = parseFloat(this.total.toFixed(2));
    console.log(this.total);
    console.log(this.totalview);
    if(this.total === 0 ) this.disabled = true;
    else this.disabled = false;
  }

  onSubmit(data) {
    if (this.auth.isLoggedIn()) {
      this.pedido = new BoostSolo(
        data["elo_atual"],
        data["fila"],
        data["pdls"],
        data["elo_final"],
        data["tecla"],
        data["chat"],
        data["lanes"],
        data["campeoes"],
        data["horario"],
        this.total,
        this.prazo);
      console.log(this.pedido);
      this.comprar.compraBoostSolo(this.pedido);
    } else {
      this.nav.onOpen();
    }
  }

  acrecimo(total,porc){
    return total*(porc/100);
  }

  desconto(total,porc){
    return -total*(porc/100);
  }


}
