export class Configjobperiodos {
  public horarios: any = {}
  //Configuração de chamadas do BD
  public service_id : number;
  public dias = [];
  public listaHorarios = [];
  constructor(data) {
    for(let item in data){
      if(item === 'service_id') this.service_id = data[item];
      else{
        this.dias.push(item);
        this.listaHorarios.push(data[item].split(','));
      }
    }
  }

  enviar(data){
    this.listaHorarios = data;
    this.horarios["service_id"] = this.service_id;
    var i = 0;
    for(let item in this.dias){
      let campo = "";
      this.listaHorarios[i].forEach((elem) => {
        campo = campo + ',' + elem;
      });
      campo = campo.slice(1,campo.length);
      this.horarios[this.dias[item]] = campo;
      i++;
    }
    return this.horarios;

  }
}
