import { UserManageService } from './../../../services/user-manage.service';
import { Perfil } from './../../../classes/Perfil';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { switchMap, catchError } from 'rxjs/operators';
import { empty, Observable } from 'rxjs';

declare var M:any;
@Component({
  selector: 'app-perfil-jobber-detalhe',
  templateUrl: './perfil-jobber-detalhe.component.html',
  styleUrls: ['./perfil-jobber-detalhe.component.css']
})
export class PerfilJobberDetalheComponent implements OnInit {
// ---------------------------------------------------- DETALHE ------------------------------------------------------------
  // Session 1
  labels1=[
    ['Nome'],
    ['Email'],
    ['Contato'],
    ['Username']
  ]

  campos1=[
    ['firstName','lastName'],
    ['address'],
    ['contact'],
    ['username']
  ]

  defineData1=[
    'simples',
    'simples',
    'simples',
    'simples'
  ]

  acoes: Function[] = [
  ];

  acaoTitulo = [
  ];

  sessions = [
    [this.labels1,this.campos1,this.defineData1]
  ]

  titleSessions = [
    ["Dados Pessoais"]
  ]

  data = 'username';

  rota = 'admProfile';

// ---------------------------------------------------- DETALHE ------------------------------------------------------------
// ---------------------------------------------------- LISTAGEM ------------------------------------------------------------
// sufix, prefix, simples, existe, progresso, op
//
displayedColumns: string[] =[
  'id',
 'titulo',
 'username_user',
 'progresso',
 'prazo',
 'opcoes'];
columnsDefinies: string[] = [
  'prefix',
 'simples',
 'simples',
 'progresso',
  'sufix',
'op'];
columnsDisplay: string[] = [
  'Pedido',
  'Titulo',
  'Cliente',
  'Progresso',
  'Prazo'];
columnsAttr: any[any] = [
  ['id', 'Pedido#'],
 'titulo',
 'username_user',
 ['progresso','progressActive'],
 ['prazo','dia(s)'],
  [['../servico/','Detalhes','id']]];

refLista = 'username';
rotaLista = 'admServicosJobber';
// ---------------------------------------------------- LISTAGEM ------------------------------------------------------------


  isRateLimitReached = false;
  profile$: Observable<Perfil>;

  constructor(
    private user: UserManageService,
    private route: ActivatedRoute,
    private router: Router
    ) {



   }
  ngOnInit(): void {

    this.profile$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) =>
      {return this.user.profile(params.get('username'));}
     ),
      catchError(error => {
        console.error(error);
        M.toast({html: 'Ops, ocorreu um erro inesperado.'})
        this.isRateLimitReached = true;
        return empty();
      })
    );
  }


}
