import { environment } from './../../../environments/environment';
import { UserManageService } from './../../arearestrita/services/user-manage.service';
import { BoostDuo } from './../classes/boostduo';
import { BoostSolo } from './../classes/boostsolo';
import { Avulso } from './../classes/avulso';
import { Compra } from './../classes/compra';
import { Md10 } from './../classes/md10';
import { Injectable } from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';



@Injectable({
  providedIn: 'root'
})
export class CompraService {

  redirectUrl: string = 'checkout';

  // Pedido em andamento
  pedido: Compra;
  checkoutPedido;
  fCompraTipo;

  // Proteção contraviolamento de injeção post

  private readonly API =  `${environment.API}`;

  constructor(private auth: AuthService, private http: HttpClient,  private router: Router, private user: UserManageService) { }

  checkout(){
    console.log(this.pedido);
    this.gerarComprar();
    this.router.navigate([this.redirectUrl]);
  }

  compram10(data : Md10){
    this.pedido = new Compra(this.user.getUser(), "M10: " + data.divisao + " - " + data.fila,data.valor,data.prazo, "m10");
    this.checkoutPedido = data;
    this.checkout();

  }

  compraAvulso(data: Avulso){
    this.pedido = new Compra(this.user.getUser(), "Vitórias Avulsas: " + data.quantidade + " vitórias - " + data.fila,data.valor,data.prazo,"avulso");
    this.checkoutPedido = data;
    this.checkout();
  }

  compraBoostSolo(data: BoostSolo){
    this.pedido = new Compra(this.user.getUser(), "EloBoost: " + data.elo_atual + " para " + data.elo_final,data.valor,data.prazo, "eloboost");
    this.checkoutPedido = data;
    this.checkout();
  }

  compraBoostDuo(data: BoostDuo){
    this.pedido = new Compra(this.user.getUser(), "DuoBoost: " + data.elo_atual + " para " + data.elo_final,data.valor,data.prazo,"duoboost");
    this.checkoutPedido = data;
    this.checkout();

  }

  gerarComprar(){
    if(this.pedido.tipo === 'm10' )
      this.fCompraTipo = function(){
        return this.http.post(this.API + 'shoppingm10',[this.checkoutPedido,this.pedido]);
      }
      if(this.pedido.tipo === 'eloboost' )
      this.fCompraTipo = function(){
        return this.http.post(this.API + 'shoppingboostsolo',[this.checkoutPedido,this.pedido]);
      }
      if(this.pedido.tipo === 'duoboost' )
      this.fCompraTipo = function(){
        return this.http.post(this.API + 'shoppingboostduo',[this.checkoutPedido,this.pedido]);
      }
      if(this.pedido.tipo === 'avulso' )
      this.fCompraTipo = function(){
        return this.http.post(this.API + 'shoppingavulso',[this.checkoutPedido,this.pedido]);
      }
  }



}
